import { transformer } from '@devsnc/sn-declarative-actions';
import { get } from 'lodash';
import { actionTypes } from '@servicenow/ui-core';
import {
	DECLARATIVE_ACTION,
	SET_CARD_LOADING,
	ADD_NOTIFICATIONS,
	DECLARATIVE_ACTION_TYPE_SERVER_SCRIPT,
	RENDER_MODE_BUTTON,
	RENDER_MODE_HEADER_DROPDOWN,
	RENDER_MODE_HEADER_ICON,
	GUIDANCE_DECLARATIVE_ACTION,
	REFRESH_REQUESTED,
	GUIDANCE_FORM_FIELDS_REQUIRED,
	SKIP_GUIDANCE_EXECUTION,
	SKIP_GUIDANCE,
	SKIP_GUIDANCE_EXECUTION_SUCCESSED,
	SKIPPED_GUIDANCE,
	SKIP_GUIDANCE_URL
} from '../../constants';
import { createHttpEffect } from '@servicenow/ui-effect-http';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

//perform variable replacement for actionPayload & actionAttributes from model
const transformDeclarativeActions = ({ action, state, updateProperties, dispatch, updateState }) => {
	const {
		properties: {
			declarativeActions,
			declarativeModel,
			formFieldItems=[]
		}
	} = state;
	if (action.type == COMPONENT_PROPERTY_CHANGED && get(action, 'payload.name') != 'declarativeActions' && get(action, 'payload.name') != 'declarativeModel')
		return;

	const evaluatedActions = transformer(declarativeActions, declarativeModel);

	let formFieldsRequired = false
	const transformedActions = evaluatedActions.reduce(
		(results, action) => {
			
			const clickActionType  = GUIDANCE_DECLARATIVE_ACTION;
			let label = action.label;

			if (action.formFieldsRequired) {
				formFieldsRequired = true;
				
				if (formFieldItems.length <= 0)
					return results;
			}

			if (action.renderMode == RENDER_MODE_BUTTON)
				results.buttonActions.push({ ...action, label, variant: action.buttonType, clickActionType, icon: action.icon || ''});
			else if (action.renderMode == RENDER_MODE_HEADER_DROPDOWN)
				results.headerDropdownActions.push({  ...action, label, variant: action.buttonType, clickActionType, icon: action.icon || ''});
			else if (action.renderMode == RENDER_MODE_HEADER_ICON)
				results.headerIconActions.push({...action, label, clickActionType, icon: action.icon || ''});

			return results;
		},
		{ buttonActions: [], headerDropdownActions: [], headerIconActions: [] }
	);
	
	updateProperties({ ...transformedActions});

	if (state.formFieldsRequired != formFieldsRequired){
		updateState({formFieldsRequired})
		dispatch(GUIDANCE_FORM_FIELDS_REQUIRED, {formFieldsRequired});
	}
		
};

const skipGuidanceExecutionEffect = createHttpEffect(
    SKIP_GUIDANCE_URL,
	{
		method: 'POST',
		dataParam: 'bodyPayLoad',
		successActionType: SKIP_GUIDANCE_EXECUTION_SUCCESSED
	}
)

export const generalActionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: transformDeclarativeActions,
	[COMPONENT_PROPERTY_CHANGED]: transformDeclarativeActions,
	[ADD_NOTIFICATIONS]: ({ dispatch }) => {
		dispatch(SET_CARD_LOADING, false);
	},
	[DECLARATIVE_ACTION]:  ({ action: { payload : {action} } , dispatch }) => {
		if (action.actionType == DECLARATIVE_ACTION_TYPE_SERVER_SCRIPT)
			dispatch(SET_CARD_LOADING, true);
	},
	[SET_CARD_LOADING]: {
		effect: ({ action: { payload }, updateProperties }) => {
			updateProperties({loading: payload});
		},
		stopPropagation: true
	},
	[REFRESH_REQUESTED]: {
		effect: ({dispatch }) => {
			dispatch(SET_CARD_LOADING, false);
		}

	},
	[SKIP_GUIDANCE]: {
		effect: ({state, dispatch}) => {
			dispatch(SET_CARD_LOADING, true);
			dispatch(SKIP_GUIDANCE_EXECUTION, {bodyPayLoad: {recommendedBy: state.properties.executionId}});
		}
	},
	[SKIP_GUIDANCE_EXECUTION]: skipGuidanceExecutionEffect,

	[SKIP_GUIDANCE_EXECUTION_SUCCESSED]: ({state, dispatch}) => {
		dispatch(SET_CARD_LOADING, false);
		dispatch(SKIPPED_GUIDANCE, {executionId: state.properties.executionId});
	}

};
