import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import {t} from 'sn-translate';

export default function renderActions(
	dropdowns,
	actions,
	dispatch,
	clickAction,
	size,
	actionDropdownOpen
) {
	if (!actions) {
		return;
	}
	if (
		(!dropdowns && actions.length < 4) ||
		(dropdowns && dropdowns.length + actions.length < 4)
	) {
		return _renderActionButtons(actions, dispatch, clickAction, size);
	} else {
		return _renderActionsDropdown(actions, size, actionDropdownOpen);
	}
}

const _renderActionButtons = (actions, dispatch, clickAction, size) => {
	return actions.map((action) => {
		return (
			<now-button-iconic
				class={{'now-card-header-actions': true, ['-' + size]: size}}
				icon={action.icon}
				disabled={action.disabled}
				variant="tertiary"
				bare
				size={size}
				hide-padding={size === 'md' ? true : undefined}
				tooltip-content={action.label}
				config-aria={{
					'aria-label': action.label
				}}
				on-click={() => {
					if (!action.disabled) {
						dispatch(() => {
							return {
								type: action.clickActionType || clickAction,
								payload: {action: action}
							};
						});
					}
				}}
			/>
		);
	});
};

const _renderActionsDropdown = (actions, size, actionDropdownOpen) => {
	return (
		<now-dropdown-custom-target
			class={{'now-card-header-actions': true, ['-' + size]: size}}
			items={actions}
			select="none"
			append-to-payload={{actionDropdown: true}}>
			<now-button-iconic
				slot="trigger"
				tooltip-content={t('Card Actions')}
				icon="ellipsis-v-outline"
				bare
				hide-padding={size === 'md' ? true : undefined}
				size={size}
				variant="tertiary"
				config-aria={{
					'aria-label': t('Card Actions'),
					'aria-haspopup': 'listbox',
					'aria-expanded': actionDropdownOpen ? 'true' : 'false'
				}}
			/>
		</now-dropdown-custom-target>
	);
};
