import { get } from 'lodash';
import { FieldType } from '@devsnc/sn-glide-form-controls';

const isGlideVarType = element => get(element, 'dictionary.type') === 'glide_var';

/*
* @param {string} fields - Comma separated field names
* @returns {string} - graphql formated string of fields
*/
export function formatFields(
	fields,
	innerFields = `{
		value
		displayValue
		label
	}`) {
	return fields
		.map(field => {
			return `
			${field} ${innerFields}
			`;
		})
		.join('\n');
}

export function indexElements(elements) {
	return elements.reduce((acc, element) => {
		acc[element.name] = element;
		return acc;
	}, {});
}

export function getElements(action) {
	return get(action, 'payload.data.GlideLayout_Query.formLayout.elementsData', []);
}

export function getValues(action) {
	return get(action, 'payload.data.GlideLayout_Query.formLayout.recordValues', []);
}

export function convertFormFields(values, formFields, elementsIndexed, associatedTable) {

	let elementsIndexedNew = {...elementsIndexed};
	return formFields.reduce((accu, field) => {
		const element = elementsIndexedNew[field];
		if (!element)
			return accu;

		if (isGlideVarType(element)) {
			element.glidevars.forEach(ele => {
				const valueName = element.name + '.' + element.modelTable + '.' + ele.name;
				let updatedField = {
					...ele,
					name: valueName
				};
				updatedField = createField(updatedField, values[valueName], associatedTable);
				accu.push(updatedField);
			});

		} else {
			const updatedField = createField(element, values[field], associatedTable);
			accu.push(updatedField);
		}

		elementsIndexedNew[field] = null;

		return accu;
	}, []);
}

function createField(element, value, associatedTable) {
	const fieldValue = (value != undefined) ? value.value : '';
	const displayValue = (value != undefined) ? value.displayValue : '';
	let field = {
		...element,
		label: element.dictionary.label,
		value: fieldValue,
		displayValue: displayValue,
		mandatory: element.dictionary.isMandatory
	};

	if (element.dictionary.type == FieldType.REFERENCE)
		field = {
			...field,
			fieldname: element.name,
			referringRecordId: (fieldValue != '' ? fieldValue : 'null'),
			referringTable: associatedTable
		};

	if (!element.dictionary.canWrite)
		field.readonly = true;

	if (element.dictionary.isReadonly)
		field.readonly = true;

	return field;
}

export function getMissingMandatoryFields(fields=[]) {
	return fields.reduce((acc, element) => {
		if (!element.value && get(element, 'dictionary.isMandatory'))
			acc.push(element.label);
		return acc;
	}, []);
}

export function validateMandatoryFields(fields=[]) {
	return fields.map(element => {
		if (get(element, 'dictionary.isMandatory'))
			return { ...element, invalid: !element.value };
		return element;
	});
}
