import {createGraphQLEffect} from '@seismic/effect-graphql';
import { createUpdateStateEffect } from '@seismic/effect-update-state';
import {LOAD_KB_ARTICLE_DETAILS,LOAD_KB_ARTICLE_DETAILS_SUCCEEDED} from '../constants'
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;

const kbDetailsQuery= `query ($queryConditions: String) {
	GlideRecord_Query {
	  kb_knowledge(queryConditions: $queryConditions) {
		_results {
		number {
			label
			value
			displayValue
          }
        short_description {
			label
			value
			displayValue
          }
		}
	  }
	}
  }
`;


const bootstrapEffect = ({ properties, dispatch, updateState }) => {
	const {
		guidanceProperties,
		guidanceCardProperties
	} = properties;

	dispatch(LOAD_KB_ARTICLE_DETAILS,{
		queryConditions:"sys_id="+guidanceProperties.kb_article.value
	})
};

export const loadKbDetailsEffect = createGraphQLEffect(kbDetailsQuery, {
	variableList: ['queryConditions'],
	successActionType: LOAD_KB_ARTICLE_DETAILS_SUCCEEDED
	});
    


export default {

	[COMPONENT_BOOTSTRAPPED] : bootstrapEffect,

	[LOAD_KB_ARTICLE_DETAILS] : loadKbDetailsEffect,
	
    [LOAD_KB_ARTICLE_DETAILS_SUCCEEDED] : ({state,action, updateState}) => {
        updateState({
            kbNumber : action['payload']['data']['GlideRecord_Query']['kb_knowledge']['_results'][0]['number']['displayValue'],
            kbShortDescription : action['payload']['data']['GlideRecord_Query']['kb_knowledge']['_results'][0]['short_description']['displayValue']
        })
    },
}