import '@servicenow/now-card';
import '@servicenow/now-loader';
import '@devsnc/sn-declarative-actions';
import '@servicenow/now-highlighted-value';
import {nowIcon} from "@servicenow/now-icon";
import { t } from 'sn-translate';
import {snGuidanceCardMediator} from '../sn-guidance-card-mediator/index';
import './sn-guidance-actions';
import '../sn-guidance-output/sn-guidance-output';

import{
	LOADING_TEXT_DEFAULT, 
	COMPLETED,
	SKIPPED
} from '../constants';


const renderForm = (state) =>{
	const {
		formFieldsRequired,
		properties: {
			formFieldItems = []
		}
	} = state;
	if (formFieldsRequired) {
		return <slot name='form' />;
	} else {
		// We will still show form fields if no Declarative Actions require them.
		// We show them as 'now-label-value-stacked' in a custom slot
		// 'form-field-stacked' to differentiate them from editable form fields.
		// This behavior is needed so not to throw users off when they see
		// 'form_fields' in the Activity Context record but don't see them
		// in the guidance Card.
		return formFieldItems.length ? <slot name='form-field-stacked' /> : null;
	}
};

export const view = (state) => {
	const {
		properties: {
			buttonActions,
			tagline,
			heading,
			caption,
			headerDropdownActions,
			headerIconActions,
			declarativeModel,
			loading,
			guidanceOutputs,
			status,
			formFieldItems = [],
			fullInputSize
		}
	} = state;

	const headerDropdown = (headerDropdownActions.length > 0) ?
		[{
			label: t('Drop down menu'),
			items: headerDropdownActions,
			icon: 'ellipsis-v-outline'
		}] : [];
	
	return (
		<sn-declarative-mediator>
			<sn-guidance-card-mediator>
				<div className='guidance-card'
					interaction='none' 
				>
					{loading ?
						<div className='loader-container'>
							<now-loader className='loader' label={ LOADING_TEXT_DEFAULT } />
						</div>
						: null
					}
					{(status !== COMPLETED && status !== SKIPPED) ? <div className='guidance-card-container'>
						<slot name='header'>
						<div className='guidance-header'>
								<now-card-header
									className='header'
									tagline={{label: t('Guidance'), icon: "lightbulb-outline"}}
									caption={caption}
									actions={headerIconActions}
									dropdowns={headerDropdown}
									append-to-payload={{ model: {...declarativeModel, fields: formFieldItems } }}
								>
								</now-card-header>
						</div>
						<div className='header'>
                			<now-card-header  heading={heading} />
            			</div>
						</slot>
						<div className='content-container'>
							<slot name='content' />
							<slot name='form' />
							<slot name='form-field-stacked' />
							<slot name='below-form'/>
						</div>
						<slot name='footer'/>
						<slot name='actions' className="card-actions">
							
							<sn-guidance-actions items={buttonActions} append-to-payload={{ model: {...declarativeModel, fields: formFieldItems } }}>
							</sn-guidance-actions>
								
						</slot>
					</div> : <slot name='outputs' className={fullInputSize? 'full-size-history': 'half-size-history'}>
						<sn-guidance-output guidanceOutputs = {guidanceOutputs} heading = {heading} status={status} fullInputSize = {fullInputSize}></sn-guidance-output>
					</slot>}
				</div>
			</sn-guidance-card-mediator>
		</sn-declarative-mediator>
	);
};
