import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import '@servicenow/now-button';
import styles from './styles.scss';

const renderButtons = (itemsObj, dispatch) => {
	if (!itemsObj || !itemsObj.length)
		return null;

	return itemsObj.map((item) => (
		<now-button
			className="action-button"
			size="sm"
			variant={item.variant}
			label={item.label}
			icon={item.icon}
			disabled={item.disabled}
			on-click={() => {
				if (!item.disabled) {
					dispatch(
						item.clickActionType,
						{	
							action: item
						}
					);
				}
			}}
		/>
	));
};

const view = (state, {dispatch}) => {
	const {
		
		properties: {items},
	} = state;
	const itemsObj = items || [];
	return (
		<div >
			{renderButtons(itemsObj, dispatch)}
		</div>
	);
};


createEnhancedElement('sn-guidance-actions', {
    view,
    properties: {
        items: {default: {}}
	},
	styles
});
