import { get } from 'lodash';
import { FieldType } from '@devsnc/sn-glide-form-controls';
import { convertNullToEmptyString } from '../utils';
import { PLAYBOOK_FIELD_LIST_EMPTY } from '../constants';

const isGlideVarType = element => get(element, 'dictionary.type') === 'glide_var';

/*
* @param {string} fields - Comma separated field names
* @returns {string} - graphql formated string of fields
*/
export function formatFields(
	fields,
	innerFields = `{
		value
		displayValue
		label
	}`) {
	return fields
		.map(field => {
			return `
			${field} ${innerFields}
			`;
		})
		.join('\n');
}

export function indexElements(elements) {
	return elements.reduce((acc, element) => {
		acc[element.name] = element;
		return acc;
	}, {});
}

export function getElements(action) {
	return get(action, 'payload.data.GlideLayout_Query.formLayout.elementsData', []);
}

export function getValues(action) {
	return get(action, 'payload.data.GlideLayout_Query.formLayout.recordValues', []);
}

export function getTableDisplayValue(action, table) {
	return get(action, 'payload.data.GlideRecord_Query.' + table + '._results[0].sys_class_name.displayValue', '');
}

export function getNumberDisplayValue(action, table) {
	return get(action, 'payload.data.GlideRecord_Query.' + table + '._results[0].number.displayValue', '');
}

export function convertElementInfo(info) {
	const {
		label = '',
		value = '',
		displayValue = '',
		type = 'string',
		reference
	} = info;

	const converted = { label, isHTMLType: false };
	
	//If the value being passed in is PLAYBOOK_FIELD_LIST_EMPTY, return that 
	//string regardless of type
	if (value === PLAYBOOK_FIELD_LIST_EMPTY) {
		converted.value = {
			type: 'string',
			value: displayValue,
		};
		return converted;
	}
	
	// 'now-label-value-stacked' won't render and fails silently if any of the
	// 'value' properties we pass to is 'null', thus needing to convert all
	// 'null' to empty string;
	switch (type) {
	case FieldType.REFERENCE:
		converted.value = {
			type: 'text-link',
			label: convertNullToEmptyString(displayValue),
			href: 'javascript:void(0)',
			underlined: true,
			table: reference,
			sysId: value
		};
		break;
	case FieldType.URL:
		converted.value = {
			type: 'text-link',
			label: displayValue,
			href: value || '',
			underlined: true,
			opensWindow: true
		};
		break;
	case FieldType.HTML:
	case FieldType.TRANSLATED_HTML:
		converted.value = {
			type: 'html',
			value: convertNullToEmptyString(value)
		};
		converted.isHTMLType = true;
		break;
	default: // Defaults to 'string' type
		converted.value = convertNullToEmptyString(displayValue);
	}

	return converted;
}

export function convertFormFields({
	elementsIndexed,
	valuesIndexed,
	formFields = [],
	associatedTable
}) {
	const formFieldItems = [];

	if (!formFields.length)
		return formFieldItems;

	const elementsIndexedNew = { ...elementsIndexed };

	formFields.forEach(field => {
		const element = elementsIndexedNew[field];

		if (!element)
			return;

		if (isGlideVarType(element)) {
			element.glidevars.forEach(ele => {
				const valueName = element.name + '.' + element.modelTable + '.' + ele.name;
				let tempFormFieldItem = { ...ele, name: valueName };
				const formFieldItem = createField(tempFormFieldItem, valuesIndexed[valueName], associatedTable);
				formFieldItems.push(formFieldItem);
			});
		} else {
			const formFieldItem = createField(element, valuesIndexed[field], associatedTable);
			formFieldItems.push(formFieldItem);
		}

		elementsIndexedNew[field] = null;
	});

	return formFieldItems;
}

export function convertFormFieldListItem(formFieldItem = {}) {
	return convertElementInfo({
		label: formFieldItem.label,
		value: formFieldItem.value || PLAYBOOK_FIELD_LIST_EMPTY,
		displayValue: formFieldItem.displayValue || PLAYBOOK_FIELD_LIST_EMPTY,
		type: get(formFieldItem, 'dictionary.type', 'string'),
		reference: formFieldItem.reference
	});
}

export const showFormFieldList = formFieldsRequired => {
	// Only show/convert 'formFieldListItems' if it is unset/'null' or
	// explicitly set to 'false'
	return formFieldsRequired === null || formFieldsRequired === false;
};

export const ensureFieldDictionaryType = (collection = {}) => {
	const filtered = [];

	for (const name in collection) {
		const field = collection[name];
		// Sometimes field items returned have invalid type properties
		if (get(field, 'dictionary.type', null))
			filtered.push(field);
	}

	return filtered;
};

function createField(element, value, associatedTable) {
	const fieldValue = (value != undefined) ? value.value : '';
	const displayValue = (value != undefined) ? value.displayValue : '';
	let field = {
		...element,
		label: element.dictionary.label,
		value: fieldValue,
		displayValue: displayValue,
		mandatory: element.dictionary.isMandatory
	};

	if (element.dictionary.type == FieldType.REFERENCE)
		field = {
			...field,
			fieldname: element.name,
			referringRecordId: (fieldValue != '' ? fieldValue : 'null'),
			referringTable: associatedTable
		};

	if (!element.dictionary.canWrite)
		field.readonly = true;

	if (element.dictionary.isReadonly)
		field.readonly = true;

	return field;
}

export function getMissingMandatoryFields(fields=[]) {
	return fields.reduce((acc, element) => {
		if (!element.value && get(element, 'dictionary.isMandatory'))
			acc.push(element.label);
		return acc;
	}, []);
}

export function validateMandatoryFields(fields=[]) {
	return fields.map(element => {
		if (get(element, 'dictionary.isMandatory'))
			return { ...element, invalid: !element.value };
		return element;
	});
}

export const collectHTMLValues = (fieldItems = []) => {
	return fieldItems.reduce((accu, item) => {
		if (get(item, 'value.type', null) === 'html')
			accu.push(item.value.value);
		return accu;
	}, []);
};

export const defaultRendererProperties = {
	/** @type {Object} miscellaneous properties */
	activityProperties: {
		default : {},
	},
	/** @type {String} the table name of the record */
	associatedTable: {
		default: '',
	},
	/** @type {String} the sys_id of the record */
	associatedSysId: {
		default: '',
	},
	/** @type {Number} sys_mod_count of the Associated Record */
	associatedRecordModCount: {
		default: 0,
	},
	activityState: {
		default: {
			value: '',
			displayValue: ''
		}
	},
	activityType:{
		default: {
			value: '',
			displayValue: ''
		}
	},
	configProperties: {
		default: {
			showActivityState: true,
			showAutomatedManualIndicator: true
		}
	},
	/**
	* @type {Boolean} Whether there are any actions which warrants a loading
	* indicator on a card
	*/
	loading: {
		default: false
	},
	/**
	* @type {Boolean} Activity Context Id
	*/
	contextId: {
		default: ''
	}
};
