import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './label-value-tabbed.scss';
import isPlainObject from 'lodash/isPlainObject';
import { omit, isEmpty, includes } from 'lodash';
import {
	IN_PROGRESS, 
	MARKED_COMPLETED, 
	COMPLETED,
	GUIDANCE_STATUS_MESSAGES,
	SKIPPED
} from '../constants'
import { t } from 'sn-translate';

const normalizeValues = (value) => {
	if (typeof value === 'string') {
		return [{type: 'string', value}];
	}
	
	if (isPlainObject(value)) {
		return [value];
	}
	return value;
}

const renderString = (
	{value}
) => {
	return (
		<div >
			<div  className="text" title={value}>
				{value}
			</div>
		</div>
	);
};

const renderItem = (item, labelIndex, componentId) => {
	let {label, value} = item;

		return (
			<Fragment>
				<dt id={`label-${componentId}-${labelIndex}`}>

					<div className="text" title={label}>
						{label}
					</div>

				</dt>
				<dd aria-labelledby={`label-${componentId}-${labelIndex}`}>
					{normalizeValues(value).map((v, i) =>
						renderString(v, i)
					)}
				</dd>
			</Fragment>
		);
	
};

const view = (state) => {
	const {componentId} = state;
	const {items, fullInputSize} = state.properties;
	return (
		<dl className={fullInputSize? "full-history-container" : "history-container"}>
			{items.map((item, i) => renderItem(item, i, componentId))}
		</dl>
	);
};

createEnhancedElement('sn-label-value-tabbed', {
	properties: {
		items: {
			default: []
		},
		fullInputSize:{
			default: false
		}
	},
    view,
    styles
	
});