import { defaultRendererProperties } from '../utils';
import { IN_PROGRESS, COMPLETED } from '../constants';

export const guidanceCardBehavior = {
	name: 'guidanceCardBehavior',
	properties: {
		guidanceProperties: {
			default : {},
		},
		guidanceOutputs:{
			default : {},
		},
		fullInputSize:{
			default: false
		},
		status:{
			default: IN_PROGRESS
		},
		/** @type {String} the table name of the record */
		associatedTable: {
			default: '',
		},
		/** @type {String} the sys_id of the record */
		associatedSysId: {
			default: '',
		},
		declarativeActions: {
			default: []
		},
		declarativeModel: {
			default: {}
		},
		parentTable: {
			default: ''
		},
		parentSysId: {
			default: ''
		},
		title:{
			default: ''
		},
		executionId:{
			default: ''
		},
		formView: {
			default: 'workspace'
		},
		guidanceCardProperties: {
			computed({
				formFieldItems,
				loading,
				properties: {
					title,
					associatedSysId,
					associatedTable,
					declarativeActions,
					declarativeModel,
					executionId,
					guidanceProperties: { icon = {} },
					guidanceOutputs,
					fullInputSize,
					status
					
				},
				formFieldsRequired,
				
			}) {
				return {
					declarativeActions,
					declarativeModel,
					...(associatedTable && {associatedTable}),
					...(associatedSysId && {associatedSysId}),
					...(title && { heading: { label: title || '', size: 'sm' } }),
					...(formFieldItems && { formFieldItems }),
					...((title  || icon.value) && { tagline: {label: title || '', icon: icon.value || 'lightbulb-outline'} }),
					loading,
					guidanceOutputs,
					fullInputSize,
					status,
					formFieldsRequired,
					executionId					
				};
			}
		}
	}
};
