import { get } from 'lodash';
import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { declarativeActionQuery, declarativeActionResponse } from './declarative-action-query';

import {
	GUIDANCE_BOOTSTRAP_DECLARATIVE_ACTIONS,
	GUIDANCE_FETCH_DECLARATIVE_ACTIONS,
	GUIDANCE_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED,
	GUIDANCE_FETCH_DECLARATIVE_ACTIONS_ERROR,
	GUIDANCE_MODEL_ID,
	GLOBAL_TABLE
} from '../constants';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const fetchGuidanceDeclarativeActionsSucceededEffect = createUpdateStateEffect((state, { payload }) => {
	const dataPath = declarativeActionResponse;
	const declarativeActions = get(payload, dataPath);

	// WIP
	// console.log('Succeeded; Actions: ', declarativeActions);

	if(!declarativeActions || declarativeActions.length == 0)
		return null;

	return {
		...state,
		declarativeActions: declarativeActions,
		properties: {
			...state.properties,
			declarativeActions: declarativeActions
		},
	};
});

const fetchGuidanceDeclarativeActions = createGraphQLEffect(
	declarativeActionQuery,
	{
		variableList: [
			'table' ,
			'sysId' ,
			'view',
			'source',
			'workspaceConfigId',
			'parentTable',
			'parentSysId'
		],
		successActionType: GUIDANCE_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED,
		errorActionType: GUIDANCE_FETCH_DECLARATIVE_ACTIONS_ERROR
	}
);

export const daActionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		dispatch(GUIDANCE_BOOTSTRAP_DECLARATIVE_ACTIONS);
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ dispatch }) => {
		dispatch(GUIDANCE_BOOTSTRAP_DECLARATIVE_ACTIONS);
	},
	[GUIDANCE_BOOTSTRAP_DECLARATIVE_ACTIONS]: ({ state, dispatch }) => {
		const {
			properties: {
				associatedTable,
				associatedSysId,
				view,
				workspaceConfigId,
				parentTable,
				parentSysId}
		} = state;

		dispatch(GUIDANCE_FETCH_DECLARATIVE_ACTIONS, {
			table: associatedTable || GLOBAL_TABLE,
			sysId: associatedSysId,
			view: view,
			source: GUIDANCE_MODEL_ID,
			workspaceConfigId,
			parentTable,
			parentSysId
		});
	},
	[GUIDANCE_FETCH_DECLARATIVE_ACTIONS]: fetchGuidanceDeclarativeActions,
	[GUIDANCE_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED]: fetchGuidanceDeclarativeActionsSucceededEffect
};
