import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import styles from './_now-card-template.scss';
import '@servicenow/now-card';
import '@servicenow/now-icon';
import {t} from 'sn-translate';
import {itemsSchema as actionsSchema} from '@servicenow/now-dropdown/src/schemas/dropdown-schema';

const _renderIdentifier = (identifier) => {
	if (identifier && identifier.type) {
		const {type, icon, src, alt} = identifier;
		return type === 'icon' ? (
			<now-icon slot="identifier" icon={icon} size="xl" />
		) : (
			<img
				slot="identifier"
				className="now-template-card-attachment-image"
				src={src}
				alt={alt}
			/>
		);
	}
};

const view = (state) => {
	const {identifier, heading, actions, caption} = state.properties;
	return (
		<now-card className="now-template-card-now-card">
			<now-card-header
				heading={{label: heading.label, level: heading.level}}
				caption={{label: caption}}
				dropdowns={
					actions
						? [
								{
									label: t('Actions'),
									icon: 'ellipsis-v-outline',
									items: actions
								}
						  ]
						: undefined
				}>
				{_renderIdentifier(identifier)}
			</now-card-header>
		</now-card>
	);
};

/**
 * Attachment template cards are a subset of the record template card.
 * Attachment template cards are best used to show a file has been or will be
 * added to a record.
 *
 * ```jsx
 * <now-template-card-attachment
 *   identifier={type: 'icon', icon: 'clipboard-outline'}
 *   heading="Pasted Image"
 *   caption="49.8 KB"
 *   actions={[
 *     {id: 'share', label: 'Copy URL'},
 *     {id: 'close', label: 'Mark Complete'}
 *   ]}
 * />
 * ```
 *
 * @seismicElement now-template-card-attachment
 * @summary Attachment template cards are best used to show a file has been or
 * will be added to a record.
 */
createEnhancedElement('now-template-card-attachment', {
	properties: {
		/**
		 * This object can be configured with the following keys:
		 * - `type: ('icon' | 'image')`: Type of identifier to display
		 *
		 * For `type` of "icon":
		 * - `icon: string`: Icon to be displayed as the identifier for the card template
		 *
		 * For `type` of "image":
		 * - `src: string`: Location of the image to display in the card template
		 * - `alt: string`: Alt tag for the <img/>
		 * @type {{type: ('icon'|'image'), icon: string, src: string, alt: string}}
		 */
		identifier: {
			schema: {
				type: 'object',
				properties: {
					type: {type: 'string', enum: ['icon', 'image']},
					icon: {type: 'string'},
					src: {type: 'string'},
					alt: {type: 'string'}
				}
			}
		},
		/**
		 * The most prominent text. This is the text description of the card.
		 * Required
		 * - `label`: Text content
		 * - `level = 3`: HTML heading element number used to render the heading
		 * @type {{label: string, level: (1|2|3|4|5|6)}}
		 */
		heading: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					level: {type: 'number', enum: [1, 2, 3, 4, 5, 6]}
				}
			}
		},
		/**
		 * Caption describing the attachment.
		 * Required
		 * @type {string}
		 */
		caption: {schema: {type: 'string'}},
		/**
		 * Array that defines actions displayed at the top of the card.
		 *
		 * The keys of each object are properties for a `now-dropdown` item.
		 * See `now-dropdown` for more information.
		 * @type {{id: string, label: string}[]}
		 */
		actions: {schema: actionsSchema}
	},
	actionHandlers: {
		'NOW_CARD_HEADER#ACTION_CLICKED': makeRedispatchEffect(
			'NOW_TEMPLATE_CARD_ATTACHMENT#ACTION_CLICKED'
		)
	},
	view,
	styles,
	dispatches: {
		/**
		 * Dispatched when the user clicks a card action in the dropdown.
		 * The payload contains the configuration of the action as passed into the
		 * `actions` property.
		 * @type {{action: Object}}
		 */
		'NOW_TEMPLATE_CARD_ATTACHMENT#ACTION_CLICKED': {}
	}
});
