import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import styles from './_now-card-template.scss';
import '@servicenow/now-card';
import '@servicenow/now-label-value';
import {t} from 'sn-translate';
import {itemsSchema as contentSchema} from '@servicenow/now-label-value/src/schemas/label-value-schema';
import {itemsSchema as actionsSchema} from '@servicenow/now-dropdown/src/schemas/dropdown-schema';

const view = (state) => {
	const {
		tagline,
		actions,
		heading,
		content,
		contentItemMinWidth,
		footerContent,
		configAria,
		selected,
		manageSelected
	} = state.properties;
	return (
		<now-card
			className="now-template-card-now-card"
			selected={selected}
			manage-selected={manageSelected}
			config-aria={{'aria-label': heading.label, ...configAria}}
			interaction="select">
			<div className="now-template-card-container">
				<now-card-header
					className="now-m-block-end--sm"
					tagline={tagline}
					heading={
						heading
							? {label: heading.label, level: heading.level, lines: 2}
							: undefined
					}
					dropdowns={
						actions
							? [
									{
										label: t('Actions'),
										icon: 'ellipsis-v-outline',
										items: actions
									}
							  ]
							: undefined
					}
				/>
				<slot>
					<now-label-value-stacked
						items={content}
						align="horizontal-equal"
						size="sm"
						item-min-width={contentItemMinWidth}
						truncated
						wrapped
					/>
				</slot>
				{footerContent ? (
					<now-card-footer className="now-template-card-footer">
						<now-label-value-inline
							label={footerContent.label}
							value={footerContent.value}
							reversed={footerContent.reversed}
						/>
					</now-card-footer>
				) : (
					undefined
				)}
			</div>
		</now-card>
	);
};

/**
 * Assist template cards are best used to expose the most relevant
 * information from an assist record to help the user make a decision
 * or to provide context.
 *
 * ```jsx
 * <now-template-card-assist
 * 	 tagline={{icon: "tree-view-long-outline", label: 'Process'}}
 * 	 actions={[
 * 	   {id: 'share', label: 'Copy URL'},
 * 	   {id: 'close', label: 'Mark Complete'}
 * 	 ]}
 * 	 heading="Submit attachment to malware sandbox and review the results"
 * 	 content={[
 * 	   {label: 'State', value: {type: 'string', value: 'Closed'}},
 * 	   {label: 'Assigned', value: {type: 'string', value: 'Carla S'}},
 * 	   {label: 'Priority', value: {type: 'string', value: 'Low'}},
 * 	   {label: 'SLA', value: {type: 'string', value: 'No SLA Set'}}
 * 	 ]}
 * 	 footer-content={{
 * 	   label: 'Updated',
 * 	   value: '2019-01-15 08:41:09'
 * 	 }}
 * />
 * ```
 *
 * @seismicElement now-template-card-assist
 * @summary Assist template cards provide a method to display a summary of an assist record.
 */
createEnhancedElement('now-template-card-assist', {
	properties: {
		/**
		 * Object with properties to configure a tagline/title for the card:
		 * - `label`: Tagline text context
		 * - `icon`: Icon that appears before the tagline `label`
		 * @type {{label: string, icon: string}}
		 */
		tagline: {
			schema: {
				type: 'object',
				properties: {label: {type: 'string'}, icon: {type: 'string'}}
			}
		},
		/**
		 * Array that defines actions displayed at the top of the card.
		 *
		 * The keys of each object are properties for a `now-dropdown` item.
		 * See `now-dropdown` for more information.
		 * @type {{id: string, label: string}[]}
		 */
		actions: {schema: actionsSchema},
		/**
		 * The most prominent text. This is the text description of the card.
		 * Required
		 * - `label`: Text content
		 * - `level = 3`: HTML heading element number used to render heading
		 * @type {{label: string, level: (1|2|3|4|5|6)}}
		 */
		heading: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					level: {type: 'number', enum: [1, 2, 3, 4, 5, 6]}
				}
			}
		},
		/**
		 * Array of objects that define the label values displayed in the content.
		 * The keys of each object are configuration for the `items` property in
		 * `now-label-value-stacked`. See `now-label-value-stacked` for more documentation.
		 *
		 * - `label: string` (Required) : Text displayed as a label
		 * - `value: (string|Object|Object[])` (Required): A single value or an array of values
		 *
		 * Each `value` can be a string for a simple value or an object for more
		 * complex values. Object values are configured with the following keys:
		 *
		 * string type:
		 * - `type: "string"`: Renders the value as a string
		 * - `value: string`: Current value
		 * - `previous: string`: Previous value (crossed out)
		 *
		 * highlighted-value type (See `now-highlighted-value` documentation for details.):
		 * - `type: "highlighted-value"`: Renders value with now-highlighted-value
		 *
		 * text-link type (See `now-text-link` documentation for details.):
		 * - `type: "text-link"`: Renders value with now-text-link
		 * @type {{label: string, values: (string|Object|Object[])}[]}
		 */
		content: {schema: contentSchema},
		/**
		 * Sets the minimum width for the items of the `content`. This is the point at
		 * which the content items wrap to a new line.
		 * Use this property in conjunction with `content`.
		 * The value in this property maps to `now-label-value-stacked[item-min-width]`
		 *
		 * The value must have units.
		 * @type {string}
		 */
		contentItemMinWidth: {schema: {type: 'string'}},
		/**
		 * Object that defines the label value displayed in the footer.
		 * The keys of the object are properties for a `now-label-value-inline`.
		 * See `now-label-value-inline` for more documentation on these properties.
		 *
		 * - `label: string` (Required): Text displayed as a label
		 * - `value: string` (Required): Text displayed as a value
		 * - `reversed: boolean`: Reverses the label and value order
		 * @type {{label: string, value: string, reversed: boolean}}
		 */
		footerContent: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					value: {type: 'string'},
					reversed: {type: 'boolean'}
				}
			}
		},
		/**
		 * An object whose items, all aria properties, are set on the inner
		 * html `<button>`.
		 * See https://www.w3.org/TR/wai-aria-1.1/#button for properties and
		 * accepted values.
		 * @type {{ 'aria-*': string }}
		 */
		configAria: {
			schema: {
				type: 'object',
				patternProperties: {
					'^aria-': {type: 'string'}
				},
				additionalProperties: false
			}
		},
		/**
		 * Maps to the `selected` property in `now-card`.
		 * Use `manage-selected` to override the default behavior and handle the
		 * `NOW_TEMPLATE_CARD_ASSIST#SELECTED_SET` action manually.
		 * @type {boolean}
		 */
		selected: {default: false, manageable: true, schema: {type: 'boolean'}}
	},
	view,
	actionHandlers: {
		'NOW_CARD#SELECTED_SET': makeRedispatchEffect(
			'NOW_TEMPLATE_CARD_ASSIST#SELECTED_SET'
		)
	},
	slots: {
		/**
		 * Content placed in the default slot of the now-template-card-assist
		 * card appear inside the body of the card, taking the place
		 * of the `content`.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	styles,
	dispatches: {
		/**
		 * Dispatched when the user clicks the card body. Set the `manage-selected`
		 * property to override the default behavior and handle this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_TEMPLATE_CARD_ASSIST#SELECTED_SET': {}
	}
});
