import {
	renderForms,
	renderFieldList,
	renderRichText
} from '../renderUtils';
import {PREVIEW_RECORD, IN_PROGRESS} from '../constants';
import {snGuidanceCard} from '../sn-guidance-card';

export const view = (state, { dispatch }) => {
    
	const {
		properties: {
			guidanceProperties,
			guidanceCardProperties
		},
    } = state;


    const getKBLink = (kbNumber,kbId) => {
        return (
            <now-text-link href = "javascript:;" label={kbNumber} variant="primary" on-click={(e) => { 
                
                dispatch(PREVIEW_RECORD, {table: 'kb_knowledge', sys_id: kbId});
            
            }}></now-text-link>
        );
    }
    
	return (
		<sn-guidance-card {...guidanceCardProperties}>
		{guidanceCardProperties.status === IN_PROGRESS?
			<div slot='content'>
				<p>{getKBLink(state.kbNumber,guidanceProperties.kb_article.value)} {state.kbShortDescription}</p>
			</div> : null	
		}
            
			{ (guidanceCardProperties.status === IN_PROGRESS)? (state.formFieldsRequired? renderForms(state, dispatch) : renderFieldList(state, 'form_fields', { slot: 'form-field-stacked', align: 'vertical' })) : null}
		</sn-guidance-card>
	);
};
