
import {snGuidedActionCard} from 'sn-guided-action-experience';

const view = (state, dispatch) => {
    const executionId = state.executionId

    if(!executionId)
        return (
            null
        );
    const props = {
        executionId,
        decisionTreeId: "decisionTreeId",
        hideHeader: true,
        fullInputSize: true
    }
   
    return (
        
        <sn-guided-decisions-card {...props}></sn-guided-decisions-card>
     
    );
    
    
};

export default view;