import {
    LOAD_NEXT_QUESTION_SUCCESSED,
    GUIDED_STRATEGY_ANSWER_UPDATED,
    SAVE_ANSWER,
    LOAD_NEXT_QUESTION_OR_ACTION,
    UPDATE_EXPANDED_PROPERTIES,
    BOOTSTRAP_GUIDED_ACTIONS,
    SAVE_ANSWER_ACTION,
    LOAD_EXECUTION_HISTORY,
    RESTART_DECISION_TREE_EXECUTION,
    SKIP_DECISION_TREE_EXECUTION,
    SKIP_GUIDED_DECISION,
    RESTART_GUIDED_DECISION,
    SKIP_DT_EXECUTION_SUCCESSED,
    LOAD_EXECUTION_HISTORY_SUCCESSED,
    GET_NEXT_QUESTION_URL,
    RESTART_EXECUTION_URL,
    SKIP_EXECUTION_URL,
    REFRESH_REQUESTED,
    GET_EXECUTION_HISTORY,
    SKIPPED_GUIDANCE,
    SKIPPED_DECISION_TREE_EXECUTION,
    MARKED_COMPLETED,
    SET_CARD_LOADING,
    MARKED_COMPLETED_DECISION_TREE_EXECUTION,
    MARK_COMPLETE_API,
    MARK_COMPLETE_SUCCESSED,
    MARK_COMPLETE,
    MARK_COMPLETE_URL,
    ADD_NOTIFICATIONS} from '../constants'
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import{
    getElements,
    getValues,
    indexElements,
    indexLabels,
    convertFormFields,
    getGuidanceProperties,
    getGuidanceName,
    getExecutionId,
    getExecutionHistory,
    getChunksOfAnswers,
    getRenderTag,
    getActionData,
    getGuidanceState,
    getActionId,
    getStatus,
    getGuidances
} from './utils';

import{
    validateMandatoryFields,
    getMissingMandatoryFields
} from '../utils';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const loadFormLayoutEffect = createHttpEffect(GET_EXECUTION_HISTORY, 
    {
		method: 'GET',
		queryParams: [
            'executionId'
		],
		
        successActionType: LOAD_EXECUTION_HISTORY_SUCCESSED
})

const updateQuestionEffect = (action, updateState) => {
    const elements = getElements(action);
	const elementsIndexed = indexElements(elements);
    const formFields = indexLabels(elements);
	const formFieldItems = convertFormFields(elementsIndexed, formFields);
    const executionId = getExecutionId(action);
    const state = getStatus(action);
    updateState({
        localFormFieldItems: formFieldItems,
        executionId,
        executionStatus: state
    });  
}

const updateActionEffect = (action, updateState) => {
   // const savedGuidances = getGuidances(action);
    let actionElement = {}
    actionElement['properties'] = getGuidanceProperties(action);
    actionElement['name'] = getGuidanceName(action);
    actionElement['Control'] = getRenderTag(action);
    actionElement['state'] = getGuidanceState(action);
    actionElement['actionData'] = getActionData(action);
    actionElement['value'] = getActionId(action);
    const state = getStatus(action);
    updateState(
        {
            actionElement: actionElement,
            executionId: getExecutionId(action),
            localFormFieldItems: [],
            executionStatus: state
        }
    )
}

const loadQuestionOrActionEffect = ({action, updateState, dispatch }) => {
    const questionsList = getElements(action);
    if(!questionsList.length){
        updateActionEffect(action, updateState);
    }else{
        updateQuestionEffect(action, updateState);
    }

    dispatch(SET_CARD_LOADING, false)
}

const updateExecutionHistory = (action , updateState, dispatch) => {
    const executionId = getExecutionId(action);
    const savedAnswers = getExecutionHistory(action);
    const state = getStatus(action);
    const savedGuidances = getGuidances(action);
    // if(state == MARKED_COMPLETED){
    //     let actionElement = {}
    //     actionElement['name'] = getGuidanceName(action);
    //     actionElement['properties'] = getGuidanceProperties(action);
    //     actionElement['Control'] = getRenderTag(action);
    //     actionElement['state'] = getGuidanceState(action);
    //     actionElement['actionData'] = getActionData(action);
    //     actionElement['value'] = getActionId(action);
    //     updateState(
    //         {
    //             actionElement: actionElement,
    //             executionId: executionId,
    //             savedAnswers: savedAnswers,
    //             localFormFieldItems: [],
    //             executionStatus: state,
    //             savedGuidances: [...savedGuidances]
    //         }
    //     )
    // }
    let elements;
    let elementsIndexed;
    let formFields;
    let formFieldItems;
    try{
         elements = getElements(action);
         elementsIndexed = indexElements(elements);
         formFields = indexLabels(elements);
         formFieldItems = convertFormFields(elementsIndexed, formFields);
        
    }catch(ex){
       
    }
    if(!formFieldItems.length){
        let actionElement = {}
        actionElement['name'] = getGuidanceName(action);
        actionElement['properties'] = getGuidanceProperties(action);
        actionElement['Control'] = getRenderTag(action);
        actionElement['state'] = getGuidanceState(action);
        actionElement['actionData'] = getActionData(action);
        actionElement['value'] = getActionId(action);
        updateState(
            {
                actionElement: actionElement,
                executionId: executionId,
                savedAnswers: getChunksOfAnswers(savedGuidances, savedAnswers),
                localFormFieldItems: [],
                executionStatus: state,
                savedGuidances: [...savedGuidances],
                currentAnswers: []
            }
        )
    }else{
        updateState({
            localFormFieldItems: formFieldItems,
            savedAnswers: getChunksOfAnswers(savedGuidances, savedAnswers),
            executionId,
            executionStatus: state,
            actionElement: {},
            savedGuidances: [...savedGuidances],
            currentAnswers: []
        });
    }
    dispatch(SET_CARD_LOADING, false);
}

const loadExecutionHistoryEffect =  ({action, updateState, dispatch }) => {
    updateExecutionHistory(action, updateState, dispatch);
    dispatch(SET_CARD_LOADING, false);
}


const answerUpdateEffect = ({ state, action, updateState }) => {
    const formFieldItems = state.localFormFieldItems.map((element) => {
        let newelement = {};
        if(element.name == action.payload.name){
            newelement = {
                ...element,
                value: action.payload.value,
                displayValue: action.payload.displayValue,
                answer: action.payload.value
            }
        }else{
            newelement = {
                ...element
            } 
        }
        
        return newelement;
    }, []);
    
    updateState({
        localFormFieldItems: formFieldItems
	});
}
const saveAnswersEffect = ({state, action, updateState, dispatch}) => {

    const validatedMandatoryFields = validateMandatoryFields(state.localFormFieldItems);
    const missingFieldLabels = getMissingMandatoryFields(validatedMandatoryFields);
    if (missingFieldLabels.length) {
        dispatch(ADD_NOTIFICATIONS, {
            notifications: [{
                type: 'info',
                message: t('The following mandatory fields are not filled in: {0}', missingFieldLabels.join(', '))
            }]
        });
        return;
    }
        

    let currentAnswers = [...state.currentAnswers];
    for(var i = 0; i < state.localFormFieldItems.length; i++){
        const newAnswer = {
            label: state.localFormFieldItems[i].label,
            value:{
                type: 'string',
                value: state.localFormFieldItems[i].displayValue
            }
        }
        currentAnswers.push(newAnswer);
    }
    updateState({
        currentAnswers: currentAnswers
    });
    
    dispatch (LOAD_NEXT_QUESTION_OR_ACTION, {savedAnswers: action.payload.savedAnswers});
    dispatch(SET_CARD_LOADING, true);
}

const updateExapandPropEffect = ({action, updateState}) => {
    const expanded = action.payload.expanded;

    updateState({
        expanded : expanded
    })
}
const saveAnsAndGetNextQuestionEffect =  createHttpEffect(
	GET_NEXT_QUESTION_URL,
	{
		method: 'POST',
		dataParam: 'savedAnswers',
		successActionType: LOAD_NEXT_QUESTION_SUCCESSED
	}
);

const restartedDTExecutionEffect = createHttpEffect(
    RESTART_EXECUTION_URL,
	{
		method: 'POST',
		dataParam: 'bodyPayLoad',
		successActionType: LOAD_EXECUTION_HISTORY_SUCCESSED
	}
);

const skipDTExecutionEffect = createHttpEffect(
    SKIP_EXECUTION_URL,
	{
		method: 'POST',
		dataParam: 'bodyPayLoad',
		successActionType: SKIP_DT_EXECUTION_SUCCESSED
	}
)

const markCompleteEffect = createHttpEffect(
    MARK_COMPLETE_URL,{
        method: 'POST',
		dataParam: 'bodyPayLoad',
		successActionType: SKIP_DT_EXECUTION_SUCCESSED
    }
)

const skipDTExecutionUpdateEffect = ({action, updateState, dispatch}) => {
    updateExecutionHistory(action, updateState, dispatch);
    dispatch(SKIPPED_DECISION_TREE_EXECUTION);
}

const makerkedCompletedEffect = ({action, updateState, dispatch}) => {
    updateExecutionHistory(action, updateState, dispatch);
    dispatch(MARKED_COMPLETED_DECISION_TREE_EXECUTION);
}

export default {
    [LOAD_EXECUTION_HISTORY] : loadFormLayoutEffect,

    [LOAD_EXECUTION_HISTORY_SUCCESSED]: loadExecutionHistoryEffect,

    [SKIP_DT_EXECUTION_SUCCESSED]: skipDTExecutionUpdateEffect,

    [LOAD_NEXT_QUESTION_SUCCESSED] : loadQuestionOrActionEffect,
    
    [GUIDED_STRATEGY_ANSWER_UPDATED]: answerUpdateEffect,

    [SAVE_ANSWER] : ({state, dispatch}) => {
        
        dispatch(SAVE_ANSWER_ACTION, {
            savedAnswers: { executionId: state.executionId, 
                            decisionTreeId: state.decisionTreeId,
                            answers: state.localFormFieldItems
                          }
            }
        );

        dispatch(SET_CARD_LOADING, true);
    },
    [SAVE_ANSWER_ACTION]: saveAnswersEffect,

    [MARK_COMPLETE] : ({state, dispatch}) => {
        dispatch(MARK_COMPLETE_API, {
            bodyPayLoad:{
                executionId: state.properties.executionId
            }
        })
    },


    [MARK_COMPLETE_API] : markCompleteEffect,

    [MARK_COMPLETE_SUCCESSED] : makerkedCompletedEffect,

    [LOAD_NEXT_QUESTION_OR_ACTION]: saveAnsAndGetNextQuestionEffect,

    [UPDATE_EXPANDED_PROPERTIES] : updateExapandPropEffect,

    [COMPONENT_BOOTSTRAPPED]: ({dispatch}) => {

        dispatch(BOOTSTRAP_GUIDED_ACTIONS);
    },

    [BOOTSTRAP_GUIDED_ACTIONS]: ({state, dispatch}) => {
        
        dispatch(LOAD_EXECUTION_HISTORY, {
            executionId: state.properties.executionId,
            decisionTreeId: state.properties.decisionTreeId
        });
        dispatch(SET_CARD_LOADING, true);
    },

    [SKIP_GUIDED_DECISION]: ({state, dispatch}) => {

        dispatch(SKIP_DECISION_TREE_EXECUTION, {
            bodyPayLoad:{
                executionId: state.properties.executionId
            }
        })
    },

    [RESTART_GUIDED_DECISION]: ({state, dispatch}) => {

        dispatch(RESTART_DECISION_TREE_EXECUTION, {
            bodyPayLoad:{
                executionId: state.properties.executionId
            }
        })

        dispatch(SET_CARD_LOADING, true);
    },

    [SET_CARD_LOADING]: {
		effect: ({ action: { payload }, updateProperties }) => {
			updateProperties({loading: payload});
		},
		stopPropagation: true
	},

    [REFRESH_REQUESTED]: {
       effect: ({state, dispatch}) => {
            dispatch(LOAD_EXECUTION_HISTORY, {
                executionId: state.properties.executionId
            })
            dispatch(SET_CARD_LOADING, true);
        },
            
        stopPropagation: true
    },

    [RESTART_DECISION_TREE_EXECUTION]: restartedDTExecutionEffect,

    [SKIP_DECISION_TREE_EXECUTION]: skipDTExecutionEffect,

    [SKIPPED_GUIDANCE]: ({state, dispatch}) => {
        dispatch(LOAD_EXECUTION_HISTORY, {executionId: state.properties.executionId});
        dispatch(SET_CARD_LOADING, true);
    },

    [ADD_NOTIFICATIONS]: ({dispatch}) => {
        dispatch(SET_CARD_LOADING, false);
    }
    
}
 