import { controlElementFactory, FieldType } from '@devsnc/sn-glide-form-controls';
import { GUIDANCE_RECORD_FIELD_UPDATE } from './constants';
// import './sn-guidance-form-section-footer';

const onValueChange = (field, dispatch) => {
	return (e, name, value, displayValue)  => {
		if (field.value !== value || field.displayValue !== displayValue) {
			let updatedField = {};

			switch (field.dictionary.type) {
			case FieldType.GLIDE_LIST:
				updatedField = {
					...field,
					value,
					displayValue: displayValue.join(', '),
					display_value_list: displayValue
				};
				break;
			default:
				updatedField = {
					...field,
					value,
					displayValue
				};
			}

			if (updatedField.mandatory) {
				updatedField = {
					...updatedField,
					invalid: !updatedField.value,
				};
			}

			dispatch (GUIDANCE_RECORD_FIELD_UPDATE, updatedField) ;
		}
	};
};


export const renderNowLabelValueStacked = (items, opts = {}) => (
	<now-label-value-stacked
		items={ items }
		align='horizontal-equal'
		size='sm'
		truncated={ true }
		wrapped={ true }
		{ ...opts }
	/>
);

export function renderFieldList(state, fieldListProperty, opts = { slot: 'content', className: 'content' } ) {
	if (!fieldListProperty)
		return null;

	const { fieldListItems = {} } = state;
	if (!fieldListItems.hasOwnProperty(fieldListProperty))
		return null;

	const { [fieldListProperty]: items = [] } = fieldListItems;
	if (!items.length)
		return null;

	return renderNowLabelValueStacked(items, opts);
}

export function renderFieldItems(state, dispatch) {
	const {
		componentId,
		formFieldItems = [],
		properties: {
			formFields
		}
	} = state;

	if (formFields == '' || (!formFieldItems || !formFieldItems.length))
		return null;

	return (
		<div slot='form'>
			{formFieldItems.map(field => {
				const { Control, props } = controlElementFactory.getFormControl(componentId, field.dictionary.type, field, onValueChange(field, dispatch), null);
				return <Control {...props}/>;
			})}
		</div>
	);
}

export function renderForms(state, dispatch){
	//Check if field items exist and render them
	const result = renderFieldItems(state, dispatch);
	if (result)
		return result;

	//if not, check if form view exists and render it
	return renderFormSection(state);
}

export function renderFormSection(state) {
	const {
		properties: {
			associatedTable,
			associatedSysId,
			formView
		}
	} = state;

	if (!formView || !associatedTable || !associatedSysId)
		return null;

	return (
		<div slot='form'>
			<now-record-form-section-connected
				table={associatedTable}
				sys-id={associatedSysId}
				view={formView}>
			</now-record-form-section-connected>
		</div>
	);
}

export const renderRichText = html => {
	if (!html)
		return null;

	return (
		<div slot='content'>
			<now-rich-text html={ html } />
		</div>
	);
};

export const renderDescription = description => {
	if (!description)
		return null;
	
	return (
		<div slot='content' className="description">
			<span>{description}</span>
		</div>
	);
};

