import { get, isEmpty } from 'lodash';
import {CARD_PROPS_TO_QUERY_MAP, 
	IN_PROGRESS, 
	MARKED_COMPLETED,
	OUTCOME_COMPLETED,
	OUTCOME_SKIPPED,
	NO_GUIDANCE,
	COMPLETED,
	GUIDANCE_STATUS_MESSAGES,
	SKIPPED,
	NEW_RECORD_ID} from '../constants'
import { t } from 'sn-translate';

export function getElements(action) {
	return get(action, 'payload.result.questions', []);
}

export function getStatus(action) {
	return get(action, 'payload.result.state', IN_PROGRESS);
}

export function getValues(action) {
	return get(action, 'payload.result.recordValues', []);
}

export function getGuidanceProperties(action) {
	return get(action, 'payload.result.guidance.guidanceProperties', {});
}

export function getGuidanceName(action){
	return get(action, 'payload.result.guidance.displayValue', '');
}

export function getExecutionId(action){
	return get(action, 'payload.result.executionId', '');
}

export function getExecutionHistory(action){
	var savedAns =  get(action, 'payload.result.history', []);
	return savedAns.reduce((accu, element) => {
		if(element.value.type == 'boolean'){
			let updateField = {
				...element,
				value:{
					type: element.value.type,
					value:element.value.value == "true"? "True" : "False"
				}

			}

			accu.push(updateField);
		}else{
			accu.push(element);
		}

		return accu;
	}, []);
}

export function getChunksOfAnswers(savedGuidances, savedAnswers){
	let itemsList = []
	if (!isEmpty(savedGuidances) && !isEmpty(savedAnswers)) {
		for(var i = 0; i < savedGuidances.length; i++){
			itemsList.push(savedAnswers.filter(item =>{

				if(i == 0){
					return  new Date(item.createDate).getTime() <=  new Date(savedGuidances[i].createDate).getTime();
				}else{
					return ( new Date(item.createDate).getTime() <=  new Date(savedGuidances[i].createDate).getTime()) && ( new Date(item.createDate).getTime() >  new Date(savedGuidances[i-1].createDate).getTime());
				}
			}));
		}
		let lastGuidance = savedGuidances[savedGuidances.length-1];
		itemsList.push(savedAnswers.filter(item =>{
				return (new Date(item.createDate).getTime() > new Date(lastGuidance.createDate).getTime());
		}));
	} else if(isEmpty(savedGuidances) && !isEmpty(savedAnswers)) {
		itemsList.push(savedAnswers);
	}
	return itemsList
}
export function sortFunction(a, b) {
	return new Date(a.createDate).getTime() - new Date(b.createDate).getTime()
}

export function getGuidances(action) {
	return  get(action, 'payload.result.guidances', []).sort(sortFunction);
}

export function getRenderTag(action){
	return get(action, 'payload.result.guidance.renderTag', 'sn-guidance-card-default');
}

export function getGuidanceState(action){
	return get(action, 'payload.result.guidance.state', IN_PROGRESS)
}

export function getActionId(action){
	return get(action, 'payload.result.guidance.value', '');
}

export function getActionData(action){
	return get(action, 'payload.result.guidance.actionData', {});
}

export function indexElements(elements) {
	return elements.reduce((acc, element) => {
		if (isGlideVarType(element)) {
			element.glidevars.forEach(ele => acc[ele.name] = ele);
		} else {
			acc[element.name] = element;
		}
		return acc;
	}, {});
}

export function indexLabels(elements) {
	return elements.map((element) => {
        return element.name;
	}, []);
}

export const isGlideVarType = element => get(element, 'dictionary.type') === 'glide_var';


export function convertFormFields(elementsIndexed, formFields) {
    
	return formFields.reduce((accu, field) => {
		const element = elementsIndexed[field];
		if (!element)
			return accu;

		if (isGlideVarType(element)) {
			element.glidevars.forEach(ele => {
				const valueName = element.name + '.' + element.modelTable + '.' + ele.name;
				let updatedField = {
					...ele,
					name: valueName
				};
				updatedField = createField(updatedField);
				accu.push(updatedField);
			});

		} else {
			const updatedField = createField(element);
			accu.push(updatedField);
		}

		return accu;
	}, []);
}

export function createField(element) {
	const fieldValue = (element.recordValue != undefined) ? element.recordValue.value : '';
	const displayValue = (element.recordValue != undefined) ? element.recordValue.displayValue : '';
	let field = {
		...element,
		label: element.dictionary.label,
		value: fieldValue,
		displayValue: displayValue,
		mandatory: element.dictionary.isMandatory
	};

	if(element.dictionary.type == 'boolean'){
		field = {
			...field,
			value: fieldValue == "true"? "true": "false",
			displayValue: fieldValue == "true"? "True" : "False"
		}
	}

	if(element.dictionary.type == 'choice'){
		field = {
			...field,
			value: fieldValue? fieldValue : ' ',
			displayValue: displayValue? displayValue: ' '
		}
	}

	if (element.dictionary.type == 'reference')
		field = {
			...field,
			fieldname: element.name,
			referringRecordId: NEW_RECORD_ID,
			referringTable: element.inputTable
		};

	if (!element.dictionary.canWrite)
		field.readonly = true;

	if (element.dictionary.isReadonly)
		field.readonly = true;

	return field;
}

export const normalizeCardProps = item => {
	return Object.entries(CARD_PROPS_TO_QUERY_MAP).reduce((properties, [cardProp, queryProp]) => {
		if (item.hasOwnProperty(queryProp))
			properties[cardProp] = item[queryProp];
		return properties;
	}, {});
};

export const normalizeGuidanceProps = item => {
	return Object.entries(CARD_PROPS_TO_QUERY_MAP).reduce((properties, [cardProp, queryProp]) => {
		if (item.hasOwnProperty(queryProp))
			properties[cardProp] = item[queryProp].value;
		return properties;
	}, {});
};

export const isExecutionSkipped = (state) => {
    const {
        executionStatus = []
    } = state;
    
    return executionStatus == SKIPPED;
}
export const isGuidancePresent = (state) => {
    return (state.actionElement && state.actionElement.value);    
}

export const isGuidanceInProgress = (state) => {
    const{
        actionElement
    } = state;

    return actionElement.state != COMPLETED && actionElement.state != SKIPPED; 
}

export const isGuidanceCompleted = (state) => {
    const{
        actionElement
    } = state;

    return actionElement.state == COMPLETED
}

export const isGuidanceSkipped = (state) => {    
    const{
        actionElement
    } = state;
    
    return actionElement.state == SKIPPED
}

export const isDTExecutionInProgress = (state) => {
    const{
        executionStatus,
        localFormFieldItems
    } = state;
  
    return localFormFieldItems && localFormFieldItems.length >  0 && executionStatus == IN_PROGRESS
}

export const isDTExecutionCompleted = (state) => {
    const{
        executionStatus
    } = state;

    return executionStatus == MARKED_COMPLETED
}

export const getGuidanceDescription = (state) => {
    if(isDTExecutionCompleted(state) && !isGuidancePresent(state))
        return t("Guidance: None")
    
    if(isExecutionSkipped(state) || (isGuidancePresent(state) && isGuidanceSkipped(state)))
        return t("Guidance ")

    return t("Guidance: ") + state.actionElement['name']
}

export const getStatusMessage = (state) => {
    if(isGuidancePresent(state) && isGuidanceCompleted(state))
        return {className: "text-positive", label: GUIDANCE_STATUS_MESSAGES[state.actionElement.state]};

    if(isExecutionSkipped(state) || (isGuidancePresent(state) && isGuidanceSkipped(state)))
        return {className: "text-negative", label: GUIDANCE_STATUS_MESSAGES['skipped']};
    
    return {className: "text-normal", label: GUIDANCE_STATUS_MESSAGES['not_available']};
    
}

export const shouldRenderNoGuidance = (state) => {
    const {
        savedAnswers,
        savedGuidances,
        localFormFieldItems,
        executionStatus,
		currentAnswers,
		actionElement
    } = state;

    let questionChunks = [...savedAnswers]
    let additionalChunkLength = currentAnswers.length ? 1 : 0;
    let noGuidanceScenario = (questionChunks.length + additionalChunkLength) !== savedGuidances.length;
	let reachedEnd = isEmpty(localFormFieldItems) && executionStatus === MARKED_COMPLETED;
	let noGuidanceValue = actionElement && isEmpty(actionElement.value);
	// (unequal chunks and saved guidances || empty guidance) && complete
	let res = (noGuidanceScenario || noGuidanceValue) && reachedEnd;
	return res;
}

export const shouldDisplayGuidance = (state) => {
    if(state.actionElement == undefined)
		return false;

	if (state.executionStatus === NO_GUIDANCE)
		return false

    if(isDTExecutionInProgress(state) || isExecutionSkipped(state))
        return false;
    
    
    if(isGuidancePresent(state) &&  !isGuidanceInProgress(state))
        return false;

    if(isDTExecutionCompleted(state) && !isGuidancePresent(state))
        return false;

    return true;

}

export const shouldDisplayDecisionsActions = (state) => {
    if(isDTExecutionCompleted(state) || isExecutionSkipped(state))
        return false;

    if(shouldDisplayGuidance(state))
        return false;

    if(state.executionStatus == OUTCOME_COMPLETED || state.executionStatus == NO_GUIDANCE || state.executionStatus == OUTCOME_SKIPPED)
        return false;

    return true;
}

export const shouldRenderGuidanceHistory = (state) => {

    if(isExecutionSkipped(state))
        return true;
    if(isDTExecutionCompleted(state) && isGuidancePresent(state) && (isGuidanceCompleted(state) || (isGuidanceSkipped(state))))
        return true;
    if(isDTExecutionCompleted(state) && !isGuidancePresent(state))
        return true;
    return false;
}