import {GUIDANCE_RECORD_FIELD_ITEMS_UPDATED, GFORM_RELOAD, REFRESH_REQUESTED} from '../constants';

const updateFormFieldsEffect = ({ state, updateState}) => {
    const {
        formFieldItems,
        properties:{
            guidanceProperties
        }
    } = state;
    
	const formFieldItemsNew = formFieldItems.map(field => {
		if (field.name === 'close_notes') {
            const fieldNew = {
                ...field,
                value: guidanceProperties.resolution_notes.value
            }
            return fieldNew
        } else if(field.name === 'resolution_code'){
            const fieldNew = {
                ...field,
                value: guidanceProperties.resolution_code.value
            }
            return fieldNew
        }

		return field;
	});

	updateState({
		formFieldItems: formFieldItemsNew
	});
}
export default{
    [GUIDANCE_RECORD_FIELD_ITEMS_UPDATED]: updateFormFieldsEffect,

    [REFRESH_REQUESTED]: {
        effect: ({dispatch}) => {
            dispatch(GFORM_RELOAD);
        }
    }
}