export function onValueChange(dispatch, field) {
    return (e, name, value, displayValue)  => {
        if (field.value !== value || field.displayValue !== displayValue) {
            let updatedField = {};
            switch (field.dictionary.type) {
            case FieldType.GLIDE_LIST:
                updatedField = {
                    ...field,
                    value,
                    displayValue: displayValue.join(', '),
                    display_value_list: displayValue
                };
                break;
            case FieldType.BOOLEAN:
                updatedField = {
                    ...field,
                    value,
                    displayValue: value? "True" : "False"
                }
                break;
         
            default:
                updatedField = {
                    ...field,
                    value,
                    displayValue: displayValue? displayValue : value
                };
            }

            if (updatedField.mandatory) {
                updatedField = {
                    ...updatedField,
                    invalid: !updatedField.value,
                };
            }
            
            dispatch (GUIDED_STRATEGY_ANSWER_UPDATED, updatedField);
        }
    };
};

export function renderGuidedActionContent(state, dispatch){


    const actionButtons = shouldDisplayDecisionsActions(state) ? [{
        id: "restart-guided-decision", 
        icon: "arrow-clockwise-fill", 
        label: t("Start Over"),
        clickActionType: RESTART_GUIDED_DECISION
    }, 
    {   id: "skip-guided-decision", 
        icon: "close-fill",
        label: t("Skip"),
        clickActionType: SKIP_GUIDED_DECISION
    }] : [];

    const{
        localFormFieldItems,
        savedGuidances,
        properties:{
            loading,
            hideHeader
        }
    } = state;

    
    
    return(
        <Fragment>
            <now-card interaction="none" className='guided-action-card'>
                {loading ?
                    <div className='loader-container'>
                        <now-loader className='loader' label={ LOADING_TEXT_DEFAULT } />
                    </div>
                    : null
				}
                <div className='guidance-header'>
                    {!hideHeader?<now-card-header 
                        className = {shouldDisplayDecisionsActions(state)? "header2" : "header1"}
                        actions = {actionButtons}
                        tagline={ {label: t("Guided Decision"), icon: "lightbulb-outline"} }  >
                    </now-card-header> : null}
                </div>
                <div className = "guided-action-card-container">
                    <div className='content-container'>
                        {renderHistory(state, dispatch)}
                        {(savedGuidances.length && (shouldDisplayQuestions(state) || shouldDisplayGuidance(state)))? 
                            <Fragment>
                                <br/>
                                <now-heading
                                has-no-margin
                                label={t("Additional Guidance")}
                                level="1" variant="header-tertiary"/>
                            </Fragment>
                            : null}
                        {renderQuestions(state, dispatch)}
                    </div>
                </div>
                
            {renderActions(state, dispatch)}
            </now-card>
        </Fragment>
    );
}

export function shouldDisplayQuestions(state){
    const{
        localFormFieldItems,
        
    } = state;
    return (localFormFieldItems && localFormFieldItems.length != 0)  
}
export function getActionPropsFromGuidance(guidance, executionId, fullInputSize){
    const dataProviderComponentId = `data-provider-123`;
	const declarativeActions =  `@${dataProviderComponentId}/declarativeActions`;

    let declarativeModelFields = {};
    let declarativeActionsProps = {};
    declarativeActionsProps['workspaceConfigId']='7b24ceae5304130084acddeeff7b12a3'
    declarativeActionsProps['view']='workspace' 
    declarativeActionsProps['component-id'] = dataProviderComponentId
    const declarativeModel = {
        guidance: ""
    };
    const properties = normalizeCardProps({
        guidanceProperties: guidance ? guidance.guidanceProperties : {},
        guidanceOutputs : guidance ? guidance.guidanceOutputs : {},
        description: t('Based on your answer(s), there is no guided decision available for this task. Please move to the next task.'),
        title : guidance ? guidance['displayValue'] : t('No Guidance Available'),
        declarativeActions,
        declarativeModel,
        executionId: executionId,
        status: guidance ? guidance.state : NOT_AVAIlABLE,
        fullInputSize: fullInputSize
    });

    declarativeActionsProps['associatedTable']= undefined
    declarativeActionsProps['associatedSysId']= undefined
    declarativeActionsProps['parentTable'] =  undefined
    declarativeActionsProps['parentSysId'] =  undefined
    
    return {
        Control: guidance ? guidance.renderTag : "sn-guidance-card-default",
        properties,
        declarativeActionsProps
    };
}
export function getActionProps(state, dispatch){
	const dataProviderComponentId = `data-provider-1234`;
	const declarativeActions =  `@${dataProviderComponentId}/declarativeActions`;

    let declarativeModelFields = {};
    let declarativeActionsProps = {};
    declarativeActionsProps['workspaceConfigId']='7b24ceae5304130084acddeeff7b12a3'
    declarativeActionsProps['view']='workspace' 
    declarativeActionsProps['component-id'] = dataProviderComponentId

    if(isGuidancePresent(state)){
        const {actionElement} = state;
        const guidanceProperties  = actionElement.properties;
        declarativeActionsProps['view'] = actionElement.formView? actionElement.formView : 'workspace'
        for (const [key, field] of Object.entries(guidanceProperties)) {
            declarativeModelFields[key] = field.value;
        }
        const declarativeModel = {
            ...declarativeModelFields,
            sysId: guidanceProperties.associated_record.value,
            table: guidanceProperties.associated_table.value,
            guidance: actionElement.value
        };

        const title = actionElement['name'];
        const props = normalizeCardProps({
            guidanceProperties: guidanceProperties,
            title,
            declarativeActions,
            declarativeModel,
            executionId: state.executionId,
            formView: actionElement.formView? actionElement.formView : 'workspace',
            status: actionElement.state
        });
        
        const properties = {
            ...normalizeGuidanceProps(guidanceProperties),
            ...props
        }
        const Control = actionElement.Control;
        
        declarativeActionsProps['associatedTable']= guidanceProperties.associated_table.value
        declarativeActionsProps['associatedSysId']= guidanceProperties.associated_record.value
        declarativeActionsProps['parentTable']=  guidanceProperties.parent_table? guidanceProperties.parent_table.value : undefined
        declarativeActionsProps['parentSysId']=  guidanceProperties.parent_record? guidanceProperties.parent_record.value : undefined
        return {
            Control,
            properties,
            declarativeActionsProps
        };
        
    }else{

        const declarativeModel = {
            guidance: ""
        };
        const title = t('No Guidance Available');
        const properties = normalizeCardProps({
            guidanceProperties: {},
            description: t('Based on your answer(s), there is no guided decision available for this task. Please move to the next task.'),
            title,
            declarativeActions,
            declarativeModel,
            executionId: state.executionId,
            status: NOT_AVAIlABLE
        });
        
        declarativeActionsProps['associatedTable']= undefined
        declarativeActionsProps['associatedSysId']= undefined
        declarativeActionsProps['parentTable'] =  undefined
        declarativeActionsProps['parentSysId'] =  undefined

        return {
            Control: "sn-guidance-card-default",
            properties,
            declarativeActionsProps
        };
    }
}

export function renderActions(state, dispatch){
    const {
        executionId
    } = state;

    let displayGuidance = shouldDisplayGuidance(state);
    let showNoGuidance = shouldRenderNoGuidance(state)
    let stateObj = !displayGuidance ? {executionId} : state;

    if (displayGuidance || showNoGuidance) {
        const {Control, properties, declarativeActionsProps} = getActionProps(stateObj);
        return (
            <Fragment>
                <sn-guidance-declarative-actions-data-provider
                    {...declarativeActionsProps}
                />
                <div className = "action_card" >
                    <Control {...properties} > </Control>
                </div>
            </Fragment>
        );
    }
}

export function renderActionFromGuidance(guidance, executionId, fullInputSize, state) {

    if ( !( (guidance && guidance.state !== IN_PROGRESS) || state.executionStatus === MARKED_COMPLETED) )
        return null
    if (isEmpty(guidance) || guidance && guidance.state === IN_PROGRESS)
        return null

    const {Control, properties, declarativeActionsProps} = getActionPropsFromGuidance(guidance, executionId, fullInputSize);

    return (
        <Fragment>
            <sn-guidance-declarative-actions-data-provider
                {...declarativeActionsProps}
            />
            <div className = "action_card" >
                <Control {...properties} > </Control>
            </div>
        </Fragment>
    );
}


export function renderHistory(state, dispatch) {
    const {
        componentId,
        savedAnswers,
        savedGuidances,
        localFormFieldItems,
        executionStatus,
        currentAnswers,
        properties:{
            fullInputSize
        }
    } = state;

    if(isEmpty(savedAnswers) && isEmpty(currentAnswers) && isEmpty(savedGuidances))
        return null;
   
    let itemsList = [...savedAnswers]

    if(!isEmpty(currentAnswers))
        itemsList.push(currentAnswers);
    let heading = {label: t("History"), size:"sm"}

    return(
        <div className="sub-content">
            <div className='header'>
                <now-card-header heading={heading} />
            </div>{
                itemsList.map((items, index) => {
                   return(
                       <div>
                            <sn-label-value-tabbed 
                                items={[...items]}
                                fullInputSize={fullInputSize}
                            />
                            {renderActionFromGuidance(savedGuidances[index], state.executionId, fullInputSize, state)}
                        </div>
                   )    
                    
                })
                
            }
           
        </div>
    );
};

export function renderQuestions(state, dispatch) {
	const {
        componentId,
        localFormFieldItems = [],
        properties:{
            hideHeader,
            fullInputSize
        }
    } = state;

    if(!localFormFieldItems || localFormFieldItems.length == 0)
        return null;
    const cardActions = []
    if(hideHeader && shouldDisplayDecisionsActions(state)){
        cardActions.push({
            id: "restart-guided-decision", 
            variant: "secondary", 
            label: t("Start Over"),
            clickActionType: RESTART_GUIDED_DECISION  
        })
        cardActions.push({   
            id: "skip-guided-decision", 
            variant: "secondary", 
            label: t("Skip"),
            clickActionType: SKIP_GUIDED_DECISION
        })
    }

    cardActions.push(
        {label: t('Next'), 
        variant: 'secondary', 
        clickActionType: SAVE_ANSWER}
    )
    let heading = {label: t("Questions"), size:"sm"}
   
    return (
        <div className="sub-content">
            <br/><br/>
            <div className='header'>
                <now-card-header  heading={heading} />
            </div>
            {getFormFields(localFormFieldItems, fullInputSize, dispatch)}
            <div className="card-actions">
                <now-card-actions items={cardActions} />
            </div>
        </div>
    );
};

const getFormFields = (localFormFieldItems, fullInputSize, dispatch) => {
    const componentId = "questions-123"
    return (
    <div className={fullInputSize?"full-container":"container"}>
                {  
                    localFormFieldItems.map(field => {
                    let { Control, props } = controlElementFactory.getFormControl(componentId, field.dictionary.type, field, onValueChange(dispatch, field), null);
                    const key = props['key']
                    delete props['component-id'];
                    delete props['key'];
                    return <div className="control" key = {key}>
                                <Control {...props} ></Control>

                                <div className='hint'><span title = {field.hint}>{field.hint}</span></div>
                            </div>;
                })}
    </div>
    );
}
