import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import styles from './_now-card-template.scss';
import '@servicenow/now-card';

const view = (state) => {
	const {tagline, heading, content, actions} = state.properties;
	return (
		<now-card className="now-template-card-now-card">
			<div className="now-template-card-container">
				<now-card-header
					className="now-m-block-end--sm"
					tagline={tagline}
					heading={
						heading
							? {label: heading.label, level: heading.level, lines: 1}
							: undefined
					}
				/>
				<slot>
					{content ? (
						<dl className="now-template-card-omnichannel-content">
							<dt className="now-line-height-crop">{content.label}</dt>
							<dd className="now-line-height-crop">{content.value}</dd>
						</dl>
					) : (
						undefined
					)}
				</slot>
				<now-card-actions
					className="now-template-card-footer"
					items={actions}
				/>
			</div>
		</now-card>
	);
};

/**
 * Omnichannel template cards provide a method to reveal content that enables a user's
 * interaction. Omnichannel template cards provide a summary and view for a task, workflow
 * or event. Omnichannel template cards allow users to quickly and conveniently take actions
 * on task, workflows, or events without going into those specific objects.
 *
 * ```jsx
 * <now-template-card-omnichannel
 * 	 tagline={{icon: "chat-outline", label: 'Chat CH0001081'}}
 * 	 heading="Something is broken"
 * 	 content={{
 * 	   label: 'Joe Employee',
 * 	   value: 'San Diego Tech Lounge'
 * 	 }}
 * 	 actions={[
 * 	   {label: 'Decline', variant: 'secondary-negative'},
 * 	   {label: 'Approve', variant: 'secondary-positive'}
 * 	 ]}
 * />
 * ```
 *
 * @seismicElement now-template-card-omnichannel
 * @summary Omnichannel template cards provide a method for revealing content that
 * enables a user's interaction.
 */
createEnhancedElement('now-template-card-omnichannel', {
	properties: {
		/**
		 * An object with properties to configure a tagline/title for the card:
		 * - `label`: Tagline text context
		 * - `icon`: Icon that appears before the tagline `label`
		 * @type {{label: string, icon: string}}
		 */
		tagline: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					icon: {type: 'string'}
				}
			}
		},
		/**
		 * The most prominent text. This is the text description of the card.
		 * Required
		 * - `label`: Text content
		 * - `level = 3`: HTML heading element number used to render a heading
		 * @type {{label: string, level: (1|2|3|4|5|6)}}
		 */
		heading: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					level: {type: 'number', enum: [1, 2, 3, 4, 5, 6]}
				}
			}
		},
		/**
		 * An object with properties to configure the label and value in the content.
		 * `label`: Label text displayed above the value
		 * `value`: Value text displayed below the label
		 * @type {{ label: string, value: string}}
		 */
		content: {
			schema: {
				type: 'object',
				properties: {
					label: {type: 'string'},
					value: {type: 'string'}
				}
			}
		},
		/**
		 * Array of objects that define buttons displayed.
		 * The keys of each object are properties for `now-button`. See
		 * `now-button` for more information on these properties.
		 * - `label: string` (Required): Label describing the action
		 * - `icon: string`: Icon displayed with the label
		 * - `variant: string`: Button style variant
		 * - `disabled: boolean`: Whether to mute the button color and disallow user click interactions
		 * - `clickActionType: string = "NOW_TEMPLATE_CARD_OMNICHANNEL#ACTION_CLICKED"`: Overrides the default action type dispatched when the user clicks on the action
		 * Required
		 * @type {{ label: string, icon: string, variant: string, disabled: boolean, clickActionType: string}[]}
		 */
		actions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						icon: {type: 'string'},
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		}
	},
	actionHandlers: {
		'NOW_CARD_ACTIONS#ACTION_CLICKED': makeRedispatchEffect(
			'NOW_TEMPLATE_CARD_OMNICHANNEL#ACTION_CLICKED'
		)
	},
	view,
	slots: {
		/**
		 * Content placed in the default slot of the now-template-card-omnichannel
		 * appears inside the body of the now-template-card-omnichannel,
		 * taking the place of the `content`.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	styles,
	dispatches: {
		/**
		 * Dispatched when a user clicks the card action button. The payload contains
		 * the configuration of the action as passed into the `actions` property.
		 * @type {{action: Object}}
		 */
		'NOW_TEMPLATE_CARD_OMNICHANNEL#ACTION_CLICKED': {}
	}
});
