import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import { guidanceCardBehavior } from '../behaviors/guidanceCardBehavior'
import { guidanceRecordBehavior } from '../behaviors/guidanceRecordBehavior'

createCustomElement('sn-guidance-card-default', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		loading: true,
		formFieldItems: []
	},
	behaviors: [
		{ behavior: guidanceCardBehavior },
		{ behavior: guidanceRecordBehavior,
			options: { fieldListProperties: ['form_fields'] }
		}
	],
	properties: {
		/**
		 * Displayed at the top of the card if present.
		 * @type {String}
		 */
		description: {
			default: ""
		}
	}
});
