
import {snGuidedActionCard} from 'sn-guided-action-experience';

const view = (state, dispatch) => {

	const {
        executionId,
        decisionTreeId
    } = state;

    const props = {
        executionId,
        decisionTreeId,
        hideHeader: false,
        fullInputSize: false
    }
   
    return (
        executionId?
        <sn-guided-decisions-card {...props}>
        </sn-guided-decisions-card> : null
    );
    
    
};

export default view;