const valueSchema = {
	type: 'object',
	properties: {
		type: {
			type: 'string',
			enum: ['string', 'text-link', 'highlighted-value', 'jsx', 'html']
		},
		value: {oneOf: [{type: 'string'}, {type: 'object'}]},
		previous: {oneOf: [{type: 'string'}, {type: 'object'}]},
		label: {type: 'string'},
		status: {type: 'string'},
		showIcon: {type: 'boolean'},
		href: {type: 'string'},
		variant: {type: 'string'},
		underlined: {type: 'boolean'},
		opensWindow: {type: 'boolean'}
	}
};

export const itemsSchema = {
	type: 'array',
	items: {
		type: 'object',
		properties: {
			label: {type: 'string'},
			value: {
				oneOf: [
					{type: 'string'},
					valueSchema,
					{type: 'array', items: valueSchema}
				]
			}
		}
	}
};
