import {
    RESTART_ACTIVITY,
    SKIP_ACTIVITY,
    RESTART_DECISION_TREE_EXECUTION,
    SKIPPED_DECISION_TREE_EXECUTION,
    SKIPPED_GUIDANCE
   } from './constants'
import { actionTypes } from '@servicenow/ui-core';
const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_CONNECTED
} = actionTypes;

const guidedDecisionsInitEffect = ({properties, updateState}) => {
    
    const{
        activityProperties  
    }=properties

    const executionId = activityProperties.execution_id.value;
    const decisionTreeId = activityProperties.decision_tree_id.value

    updateState({
        executionId,
        decisionTreeId
    })

}

export default {
    [RESTART_DECISION_TREE_EXECUTION]: ({state, dispatch}) => {
        //need to dispatch restart activity 
        console.log("Dispatched restart activity");
    },

    [COMPONENT_BOOTSTRAPPED]: guidedDecisionsInitEffect,

    [COMPONENT_CONNECTED]: ({updateState, action: {payload: {activityProperties}}}) => {
       
        const {
            execution_id,
            decision_tree_id
        } = activityProperties;

        updateState({
            executionId: execution_id.value,
            decisionTreeId: decision_tree_id.value
        });
    },
    [COMPONENT_PROPERTY_CHANGED]: ({ action,updateState }) => {
        
        const {
            payload:{
			    name,
                value
            }
		} = action;
			
		if (name === 'activityProperties'){
            const{
                execution_id,
                decision_tree_id
            } = value
            updateState({
                executionId: execution_id.value,
                decisionTreeId: decision_tree_id.value
			});
        }
			
	}
    
}


 