import '@servicenow/now-dropdown';

export default function renderDropdowns(dropdowns, size, openDropdowns) {
	if (!dropdowns) {
		return '';
	}
	return dropdowns.map((dropdown) => {
		return (
			<now-dropdown-custom-target
				class={{'now-card-header-actions': true, ['-' + size]: size}}
				items={dropdown.items}
				disabled={dropdown.disabled}
				select="none"
				append-to-payload={{dropdownId: dropdown.id}}>
				<now-button-iconic
					slot="trigger"
					icon={dropdown.icon}
					tooltip-content={dropdown.label}
					bare
					size={size}
					hide-padding={size === 'md' ? true : undefined}
					variant="tertiary"
					config-aria={{
						'aria-label': dropdown.label,
						'aria-haspopup': 'listbox',
						'aria-expanded': openDropdowns[dropdown.id] ? 'true' : 'false'
					}}
				/>
			</now-dropdown-custom-target>
		);
	});
}
