export const declarativeActionQuery = `
	query ($table: String!, $sysId: String, $view: String!, $source: String!, $workspaceConfigId: String, $parentTable: String, $parentSysId: String) {
		GlideDeclarativeActions_Query {
			GlideDeclarativeActions_genericQuery {
				genericDeclarativeActions(table: $table, sysId: $sysId, view: $view, source: $source, workspaceConfigId: $workspaceConfigId, relatedParentRecordTable: $parentTable, relatedParentRecordSysId: $parentSysId) {
					genericActions {
						actions {
							actionAttributes
							actionComponent
							actionDispatch
							actionPayload
							actionType
							assignmentId
							buttonType
							conditions
							confirmationMessage
							confirmationRequired
							dependency
							groupBy
							group
							icon
							label
							modelFieldRequired
							name
							order
							payloadMap {
								name
								value
							}
							recordSelectionRequired
							formFieldsRequired
							requiresValue
							renderMode
							table
							tooltip
							view
							modelConditions {
								field
								operator
								value
								newQuery
								or
							}
						}
					}
				}
			}
		}
	}
`;

export const declarativeActionResponse = 'data.GlideDeclarativeActions_Query.GlideDeclarativeActions_genericQuery.genericDeclarativeActions.genericActions[0].actions';
