import {
    LOAD_EXECUTION_ID,
    LOAD_EXECUTION_ID_SUCCESSED,
    RESTART_DECISION_TREE_EXECUTION,
    SKIPPED_DECISION_TREE_EXECUTION
   } from './constants'
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { actionTypes } from '@servicenow/ui-core';
import { get } from 'lodash';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED, COMPONENT_CONNECTED} = actionTypes;

const fetchExecutionEffect = createHttpEffect('/api/now/guided_decisions_api/get_execution_id', 
{
    method: 'GET',
    queryParams: [
        'taskTable',
        'taskRecord'
    ],
    
    successActionType: LOAD_EXECUTION_ID_SUCCESSED
})

const fetchExecutionPrepareEffect = ({state, action, updateState, dispatch}) => {
    const taskTable = state.properties.taskTable;
    const taskRecord = state.properties.taskRecord;
    dispatch(LOAD_EXECUTION_ID, {
        taskTable: taskTable,
        taskRecord: taskRecord
    })
    
}

const updateExecutionIDEffect = ({action, updateState}) => {
    const executionId = get(action, 'payload.result.executionId', '');
    updateState({
        executionId
    })
}

export default {
    [COMPONENT_BOOTSTRAPPED]: fetchExecutionPrepareEffect,

    [COMPONENT_PROPERTY_CHANGED]: fetchExecutionPrepareEffect,

    [LOAD_EXECUTION_ID]: fetchExecutionEffect,

    [LOAD_EXECUTION_ID_SUCCESSED]: updateExecutionIDEffect,

    [RESTART_DECISION_TREE_EXECUTION]: ({state, dispatch}) => {
        //need to dispatch restart activity 
        console.log("Dispatched restart activity");
    },

    [SKIPPED_DECISION_TREE_EXECUTION]: ({state, dispatch}) => {
        //need to dispatch skip activity 
        console.log("Dispatched skip activity");
    },

    
}


 