import { PLAYBOOK_CARD } from '../constants';
import { createCustomElement} from '@servicenow/ui-core';
import { daActionHandlers } from './actions';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

/**
 * ## Declarative Actions Data Provider
 *
 * Loads declarative actions for a given card by:
 * - Table (associated-table)
 * - SysId (associated-sys-id)
 * - Parent Record Table (input-table)
 * - Parent Record SysId (input-sys-id)
 *
 * Guidance Properties:
 * associated-table, associated sys-id, input-table, input-sys-id, declarative-actions(selectable)
 *
 * There is one declarative action data provider for each card.
 *
 * @summary Declarative Action Data Provider for cards.
 * @seismicElement sn-guidance-declarative-actions-data-provider
 */
export default createCustomElement('sn-guidance-declarative-actions-data-provider', {
	renderer: {
		type: snabbdom
	},
	properties: {
		/**
		 * the table name of the record
		 * @type {String}
		 */
		associatedTable: {
			default: '',
		},
		/**
		 * the sys_id of the record
		 * @type {String}
		 */
		associatedSysId: {
			default: '',
		},
		/**
		 * the table of the parent record
		 * @type {String}
		 */
		parentTable: {
			default: '',
		},
		/**
		 * the sys_id of the parent record
		 * @type {String}
		 */
		parentSysId: {
			default: '',
		},
		/**
		 * the workspace Config Id to display record in
		 * @type {String}
		 */
		workspaceConfigId: {
			default: '',
		},
		/**
		 * the view to display record in
		 * @type {String}
		 */
		view: {
			default: PLAYBOOK_CARD,
		},
		/**
		 * Declarative Actions selector
		 * @type {Array}
		 */
		declarativeActions: {
			selectable: true,
			default: []
		},
	},

	actionHandlers: daActionHandlers
});
