import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import {
	DECLARATIVE_ACTION,
	GUIDANCE_DECLARATIVE_ACTION,
	ADD_NOTIFICATIONS
} from '../constants';
import { getMissingMandatoryFields, validateMandatoryFields } from '../utils';
import { t } from 'sn-translate';

export const view = () => {
	return <slot />;
};


const declarativeActionClickEffect = ({
	action: {
		payload: { action, model}
	},
	dispatch
}) => {
	const validatedMandatoryFields = validateMandatoryFields(model.fields);
	const missingFieldLabels = getMissingMandatoryFields(validatedMandatoryFields);
	if (missingFieldLabels.length) {
		dispatch(ADD_NOTIFICATIONS, {
			notifications: [{
				type: 'info',
				message: t('The following mandatory fields are not filled in: {0}', missingFieldLabels.join(', '))
			}]
		});
		return;
	}

	dispatch(DECLARATIVE_ACTION, { action, model: {
		...model,
		fields: [...validatedMandatoryFields]
	} });
}

createCustomElement('sn-guidance-card-mediator', {
	renderer: {
		type: snabbdom,
		view
	},
	actionHandlers: {
		
		[GUIDANCE_DECLARATIVE_ACTION]: declarativeActionClickEffect
	}
});
