import { get } from 'lodash';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { FieldType } from '@devsnc/sn-glide-form-controls';

import {
	GUIDANCE_RECORD_FETCH,
	GUIDANCE_RECORD_STARTED,
	GUIDANCE_RECORD_SUCCEEDED,
	GUIDANCE_RECORD_FAILED,
	GUIDANCE_RECORD_INIT,
	GUIDANCE_RECORD_FIELD_UPDATE,
	GUIDANCE_RECORD_SET_FIELDS,
	GUIDANCE_FORM_FIELDS_REQUIRED,
	PREVIEW_RECORD,
	NOW_TEXT_LINK_CLICKED,
	NOW_LABEL_VALUE_STACKED_TEXT_LINK_CLICKED,
	GUIDANCE_RECORD_FIELD_ITEMS_UPDATED
} from '../constants';
import { recordQuery } from '../gql.constants';
import { actionTypes } from '@servicenow/ui-core';
import {
	formatFields,
	convertFormFields,
	getElements,
	getValues,
	indexElements,
} from '../utils';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED, COMPONENT_CONNECTED } = actionTypes;

const fetchRecordEffect = createGraphQLEffect(recordQuery, {
	templateVarList: ['associatedTable', 'fields'],
	variableList: ['sysId', 'table', 'view', 'columns'],
	startActionType: GUIDANCE_RECORD_STARTED,
	successActionType: GUIDANCE_RECORD_SUCCEEDED,
	errorActionType: GUIDANCE_RECORD_FAILED
});

const updateLoadingEffect = loading =>
	createUpdateStateEffect(state => {
		return {
			...state,
			loading: loading
		};
	});

const recordSucceededEffect = ({ properties, action, updateState, state, dispatch }) => {
	const { formFields, associatedTable } = properties;
	const error = get(action, 'error', false);
	const errors = get(action, 'payload.errors', []);
	const hasErrors = !!(error || (errors && errors.length));
	const elements = getElements(action);
	const values = getValues(action);
	const elementsIndexed = indexElements(elements);
	const valuesIndexed = indexElements(values);
	const formFieldItems = formFields.length ? convertFormFields(valuesIndexed, formFields, elementsIndexed, associatedTable): [];
	const fieldListItems = parseFieldList({
		state,
		formFieldItems
	});

	updateState({
		formFieldItems,
		fieldListItems,
		loading: false,
		error: hasErrors
	});

	dispatch(GUIDANCE_RECORD_FIELD_ITEMS_UPDATED, {formFieldItems});

};


function parseFieldList({state: {fieldListProperties = []}, formFieldItems}){
	const htmlValues = [];
	const fieldListItems = fieldListProperties.reduce((result, prop)=>{

		result[prop] = formFieldItems.map(({label, value, displayValue, dictionary: {type = 'string', reference} ={} })=>{

			switch (type) {
			case FieldType.REFERENCE:
				return {
					label,
					value: {
						type: 'text-link',
						label: displayValue,
						href: 'javascript:void(0)',
						underlined: true,
						table: reference,
						sysId: value
					}
				};
			case FieldType.URL:
				return {
					label,
					value: {
						type: 'text-link',
						label: displayValue,
						href: value || '',
						underlined: true,
						opensWindow: true
					}
				};
			case FieldType.HTML:
			case FieldType.TRANSLATED_HTML:
				htmlValues.push(value);
				return {
					label,
					value: {
						type: 'html',
						value: value || ''
					}
				};
			default:
				return {
					label,
					value: displayValue || ''
				};
			}
		});

		return result;
	}, {});
	

	return fieldListItems;
}

const recordInitEffect = ({ properties, dispatch, updateState }) => {
	const {
		formFields,
		formView,
		associatedTable,
		associatedSysId,
	} = properties;
	let fields = [];
	fields = fields.concat(formFields);

	if (associatedTable && associatedSysId && fields.length>0) {
		dispatch(GUIDANCE_RECORD_FETCH, {
			table: associatedTable,
			associatedTable: associatedTable,
			sysId: associatedSysId,
			view: formView,
			fields: formatFields(fields),
			columns: fields.join(',')
		});
	} else {
		updateState({
			loading: false
		});
	}
};

const propertyChangeEffect = ({ action, dispatch }) => {
	const { payload: {name}} = action;
	if (name != 'declarativeModel' && name != 'modalAction' && name != 'declarativeActions')
		dispatch(GUIDANCE_RECORD_INIT);
};

const updateFieldEffect = ({ action, updateState, state: { formFieldItems = [] } }) => {
	const { payload } = action;
	const formFieldItemsNew = formFieldItems.map(field => {
		if (field.name === payload.name) return payload;

		return field;
	});

	updateState({
		formFieldItems: formFieldItemsNew
	});
};

const setFieldsEffect = ({ action: { payload }, updateState }) => {
	const fields = (Object.keys(payload));
	const formFieldItems = fields.map(field => {
		return payload[field];
	});

	updateState({
		formFieldItems: formFieldItems
	});
};

const setFormFieldsRequired = ({ action, state, updateState }) => {
	const formFieldsRequired = get(action, 'payload.formFieldsRequired', null);

	updateState({ formFieldsRequired });
};


export const guidanceRecordBehavior = {
	name: 'guidanceRecordBehavior',
	initialState: {
		
		formFieldsRequired: null
	},
	properties: {
		/**
		 * Display the record fields with the values filled in
		 * @type {Array}
		 */
		recordFields: {
			computed({
				properties: {
					guidanceProperties: { record_fields }
				}
			}) {
				const value = record_fields ? record_fields.value : '';
				return value && value.length ? value.split(',') : [];
			}
		},
		/**
		 * The list of field names will display in a form
		 * with the current values filled in.
		 * @type {Array}
		 */
		formFields: {
			computed({
				properties: {
					guidanceProperties: { form_fields }
				}
			}) {
				const value = form_fields ? form_fields.value : '';
				return value && value.length ? value.split(',') : [];
			}
		}
	},
	actionHandlers: {
		[COMPONENT_CONNECTED]({updateState, action: {payload: {options}}}){
			const {fieldListProperties = []} = options;
			updateState({fieldListProperties});
		},
		[GUIDANCE_RECORD_INIT]: recordInitEffect,
		[COMPONENT_BOOTSTRAPPED]: recordInitEffect,
		[COMPONENT_PROPERTY_CHANGED]: propertyChangeEffect,
		[GUIDANCE_RECORD_FETCH]: fetchRecordEffect,
		[GUIDANCE_RECORD_STARTED]: updateLoadingEffect(true),
		[GUIDANCE_RECORD_SUCCEEDED]: recordSucceededEffect,
		[GUIDANCE_RECORD_FAILED]: recordSucceededEffect,
		[GUIDANCE_RECORD_FIELD_UPDATE]: updateFieldEffect,
		[NOW_TEXT_LINK_CLICKED]: {
			effect: ({ dispatch, action : {payload: { table, sys_id, recordLink } }}) => {
				if (!table || !sys_id || !recordLink)
					return;
				dispatch(PREVIEW_RECORD, {
					table,
					sys_id
				});
			},
			stopPropagation: true
		},
		[NOW_LABEL_VALUE_STACKED_TEXT_LINK_CLICKED]: {
			effect: ({ action, dispatch }) => {
				const { payload: {item} } = action;
				if(item && item.table && item.sysId) {
					dispatch(PREVIEW_RECORD, {
						table: item.table,
						sys_id: item.sysId
					});
				}
			},
			stopPropagation: false
		},

		[GUIDANCE_FORM_FIELDS_REQUIRED]: {
			effect: setFormFieldsRequired,
			stopPropagation: true
		}
	}
};
