import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import { guidanceCardBehavior } from '../behaviors/guidanceCardBehavior'
import { guidanceRecordBehavior } from '../behaviors/guidanceRecordBehavior'
import actionHandlers from './actions';

/**
 *
 * Link KB Article card that accepts these activityProperties:
 * - title
 * - description
 * - recordFields
 * - formFields
 * - showChecklist
 * - formView
 *
 * @summary The Link KB Article rendering
 * @seismicElement sn-guidance-link-kb-card
 */
createCustomElement('sn-guidance-link-kb-card', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		loading: true,
        formFieldItems: [],
        kbNumber: '',
        kbShortDescription: ''
	},
	behaviors: [
		{ behavior: guidanceCardBehavior },
		{ behavior: guidanceRecordBehavior,
			options: { fieldListProperties: ['form_fields'] }
		}
	],
	properties: {
		/**
		 * Displayed at the top of the card if present.
		 * @type {String}
		 */

		title:{
			default: ""
		},
    },
	actionHandlers
});
