import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import { omit, isEmpty, includes, get } from 'lodash';
import styles from './guidance-output.scss';
import {GUIDANCE_STATUS_MESSAGES, COMPLETED} from '../constants';

const sanitizeOutputObjects = (guidanceOutputObj) => {
    let meta = guidanceOutputObj.meta;
	let outputs = [];
	for (let i = 0; !isEmpty(meta) && i < meta.length; i++) {
		let value = guidanceOutputObj[meta[i].varName];
		let defaultValue = meta[i].varDefaultValue;
		outputs.push({
			tableName: meta[i].varTableName,
			type: meta[i].varType,
			value: isEmpty(value) ?  defaultValue : value,
			label: meta[i].varlabel,
			order: meta[i].varOrder,
			active: meta[i].varActive === 'true',
			displayValue: meta[i].varDisplayValue
		});
	}
	return outputs;
}

const getActualGuidanceOutputs = (obj) => {
	let guidanceOutputObj = omit(obj, ['sys_meta', 'sys_id']);
	return sanitizeOutputObjects(guidanceOutputObj);

}


// Returns true if string or guid
// false if others
const isOutputTypeSupported = (output) => {
	return output.type == 'string' || output.type == 'reference';
}

const isOutputExisting = (output) => !isEmpty(output.value)

const getGuidanceRenderValue = (guidanceOutputObj) => {
	// If the value is reference display a link
	// if the value is string just display it
	if (guidanceOutputObj.type == 'string') {
		return <div>{`${guidanceOutputObj.label}: ${guidanceOutputObj.value}`}</div>;
	} else if (guidanceOutputObj.type === 'reference') {
		let addr = `/${guidanceOutputObj.tableName}.do?sys_id=${guidanceOutputObj.value}`;
		let referenceElement = !isEmpty(guidanceOutputObj.displayValue) ?
			<Fragment>
				{guidanceOutputObj.label} : <now-text-link
					label={guidanceOutputObj.displayValue}
					variant="primary"
					href='javascript:void(0);'
					append-to-payload={{
						tableName: guidanceOutputObj.tableName,
						sysId: guidanceOutputObj.value
					}}
				/>
			</Fragment> :
			<now-text-link
				label={guidanceOutputObj.label}
				variant="primary"
				href='javascript:void(0);'
				append-to-payload={{
					tableName: guidanceOutputObj.tableName,
					sysId: guidanceOutputObj.value
				}}
			/>
		return (<div>{referenceElement}</div>);
	}
}

const renderGuidanceValue = (guidanceOutputObjArr, status) => {
	var outputElements = guidanceOutputObjArr.reduce((a, v) => {
		let outputObj = isOutputExisting(v) && isOutputTypeSupported(v) ? <div>{getGuidanceRenderValue(v)}</div> : '';
		if (!isEmpty(outputObj) && !includes(a, outputObj) && v.active) {
			a.push(outputObj)
		}
		return a;
	}, []);
	return (
		<span >
			<div className = {status === COMPLETED? 'text-positive': 'text-negative'}>{GUIDANCE_STATUS_MESSAGES[status]}</div>
			{outputElements}
		</span>
	)
}
           
const view = (state) => {
    const{
        properties:{
            guidanceOutputs,
            status,
            heading,
            fullInputSize
        }
    } = state;

	let guidanceOutputObjArr = getActualGuidanceOutputs(guidanceOutputs);
	guidanceOutputObjArr = isEmpty(guidanceOutputs) ? [] : guidanceOutputObjArr;
	guidanceOutputObjArr.sort((a, b) => a.order-b.order)

    return (
		<div>
			<div className = 'guidance-history'>
				<hr/>
                <div className={fullInputSize? "full-history-container" : "history-container"}>
                    <div className="guidance-description">
                        <span title = {heading.label}>{heading.label}</span>
                    </div>
                    <div className = "status">
                        {renderGuidanceValue(guidanceOutputObjArr, status)}
                    </div>
				
                </div>
				</div>
				
		</div>
		
	)
}

const handleReferenceClick = (coeffects) => {
	const { state, dispatch, updateState, action } = coeffects;

	let sysId = get(action, 'payload.sysId', null);
	let tableName = get(action, 'payload.tableName', null);

	if (sysId && tableName) {
		dispatch('ITEM_SELECTED', { 'table': tableName, 'sys_id': sysId});
	}
};

createEnhancedElement('sn-guidance-output', {
	properties: {
		guidanceOutputs:{
            default: {}
        },
        heading: {
            default: {}
        },
        status: {
            default: 'completed'
        },
        fullInputSize:{
			default: false
		}
		
	},
    view,
    styles,
    actionHandlers: {
		['NOW_TEXT_LINK#CLICKED']: handleReferenceClick,
    }
	
});