import { defaultRendererProperties } from '../utils';
import {
	PLAYBOOK_CARD_AUTOMATED_ICON,
	PLAYBOOK_CARD_AUTOMATED_TAGLINE,
	PLAYBOOK_SUFFICIENT_DATA
} from '../../constants';

export const playbookCardBehavior = {
	name: 'playbookCardBehavior',
	properties: {
		...defaultRendererProperties,
		declarativeActions: {
			default: []
		},
		declarativeModel: {
			default: {}
		},
		parentTable: {
			default: ''
		},
		parentSysId: {
			default: ''
		},
		filterData: {
			default: []
		},
		assignedTo: {
			default: {}
		},
		playbookCardProperties: {
			computed({
				formFieldItems,
				title: stateTitle,
				notification,
				hasSufficientData = true,
				properties: {
					associatedSysId,
					associatedTable,
					declarativeActions,
					declarativeModel,
					activityState,
					activityProperties: {
						pending_title = {},
						pending_description = {},
						title = {},
						footer,
						is_automated = {},
						show_sla = {},
						tagline = {},
						icon = {}
					},
					configProperties,
					filterData,
					assignedTo,
					formFieldsRequired,
					loading,
					contextId
				},
				formFieldsInModal
			}) {
				// Override icon / tagline if automated
				if (is_automated.value === '1') {
					icon.value = PLAYBOOK_CARD_AUTOMATED_ICON;
					icon.displayValue = PLAYBOOK_CARD_AUTOMATED_ICON;
					tagline.value = PLAYBOOK_CARD_AUTOMATED_TAGLINE;
					tagline.displayValue = PLAYBOOK_CARD_AUTOMATED_TAGLINE;
				}

				return {
					declarativeActions,
					declarativeModel,
					activityState,
					...(associatedTable && {associatedTable}),
					...(associatedSysId && {associatedSysId}),
					...(title.value && { heading: { label: title.value } }),
					// titles stored by card components in state, take precedence  over those passed down in activity properties
					...(stateTitle && { heading: { label: stateTitle } }),
					...(formFieldItems && { formFieldItems }),
					...((tagline.value || icon.value) && { tagline: {label: tagline.value || '', icon: icon.value || ''} }),
					...(pending_title.value && {pendingTitle: pending_title.value}),
					...(pending_description.value && {pendingDescription: pending_description.value}),
					loading,
					contextId,
					configProperties,
					cardFooter: footer,
					filterData,
					assignedTo,
					formFieldsRequired,
					...(notification && {notification}),
					formFieldsInModal,
					showSla: show_sla.value === '1',
					hasSufficientData
				};
			}
		},
	},
	actionHandlers: {
		[PLAYBOOK_SUFFICIENT_DATA]: ({action: {payload}, updateState}) => {
			const {hasSufficientData = null} = payload;
			if(hasSufficientData === null)
				return;

			updateState({hasSufficientData});
		}
	}
};
