import {
	renderForms,
	renderFieldList,
	renderDescription
} from '../renderUtils';

import {snGuidanceCard} from '../sn-guidance-card';
import { COMPLETED, IN_PROGRESS } from '../constants';

export const view = (state, { dispatch }) => {
	
	const {
		properties: {
			description,
			guidanceCardProperties
		},
	} = state;
	
	return (
		<sn-guidance-card {...guidanceCardProperties}>
		{guidanceCardProperties.status === IN_PROGRESS? renderDescription(description) : null}

		{ guidanceCardProperties.status === IN_PROGRESS?(state.formFieldsRequired? renderForms(state, dispatch) : renderFieldList(state, 'form_fields', { slot: 'form-field-stacked', align: 'vertical' })): null}
		</sn-guidance-card>
	);
};
